export class TTassParameters {
    M1_cali: number;
    M1_L: number;
    M1_H: number;

    M2_cali: number;
    M2_L: number;
    M2_H: number;

    W3_cali: number;
    W3_L: number;
    W3_H: number;

    LA_T: number;
    SB_RNG: number;
    SB_T: number;
    MS: number;
    T1: number;
    AL_Min: number;
    AR_Min: number;
    AM_MinR: number;
    AL_AR_MinR: number;
}

export class TTassCurve {
    PointCount: number;
    A: number;
    T: number;
}

export class TTassLinReg {
    dM: number;
    dB: number;
    dR: number;
    idxCurveBeg: number;
    iPointCount: number;
}

export class TTassResult {
    Result: number;

    M1: number;
    M2: number;
    LA: number;
    W3: number;

    W_Start: number;
    W_End: number;
    W1: number;
    W2: number;
    W_AM_left: number;
    W_AM_middle: number;
    W_AM_right: number;

    G_AL: TTassLinReg;
    G_AM: TTassLinReg;
    G_AR: TTassLinReg;

    idx_W_Start: number;
    idx_W_End: number;
    idx_W_AM_left: number;
    idx_W_AM_middle: number;
    idx_W_AM_right: number;

    idxNormBeg: number;
    idxNormEnd: number;
}

export enum TassErrCode {
    // Generic errors
    TASS_E_WIN32ERROR     	= 0xEE000000,    // Win32 error occured. Use GetLastError()
    TASS_E_BUFTOOSMALL    	= 0xEE000001,    // Buffer too small
    TASS_E_INVALIDARG     	= 0xEE000002,    // invalid argument passed to function
    TASS_E_INVALIDOBJ     	= 0xEE000003,    // invalid object
    TASS_E_INVALIDVER     	= 0xEE000004,    // invalid version
    TASS_E_SIZEMISMATCH   	= 0xEE000005,    // size mismatch
    TASS_E_CRCERROR       	= 0xEE000006,    // CRC wrong
    TASS_E_UNCOMPRESS     	= 0xEE000007,    // uncompress error
    TASS_E_FILEREAD       	= 0xEE000008,    // read file error
    TASS_E_INVALIDHDR     	= 0xEE000009,    // invalid header/structure
    TASS_E_INVALIDADR     	= 0xEE00000A,    // invalid address
    TASS_E_NOTFOUNDKE     	= 0xEE00000B,    // KE not found in internal list
    TASS_E_IDNOTUNIQUE    	= 0xEE00000C,    // Given ID is not unique
    TASS_E_NOTFOUNDID     	= 0xEE00000D,    // Given ID not found (KE/SE)
    TASS_E_JOBNOTRDY      	= 0xEE00000E,    // Job is not in correct state: job not ready
    TASS_E_INVALIDPRG     	= 0xEE00000F,    // invalid program number
    TASS_E_NOTIMPL     	  	= 0xEE000010,	// function is not implemented
    // TASS specific errors
    TASS_E_NO_POINTS        = 0xEE001001,    // no points given/found
    TASS_E_TOOMANY_POINTS   = 0xEE001002,    // too many points given
    TASS_E_NO_THRESHOLD     = 0xEE001003,    // no points above threshold found
    TASS_E_REGRESSION       = 0xEE001004,    // error calculating regression
    TASS_E_REG_MIDDLE       = 0xEE001005,    // error calculating the regressing for the middle area
    TASS_E_NO_LEFT          = 0xEE001006,    // no points found in the left area
    TASS_E_NO_RIGHT         = 0xEE001007,    // no points found in the right area
    TASS_E_REG_LEFT         = 0xEE001008,    // error calculating the regressing for the left area
    TASS_E_REG_RIGHT        = 0xEE001009,  	// error calculating the regressing for the right area
    TASS_E_SBT_ABOVE        = 0xEE00100A,    // point outside (above) SB_T hull found
    TASS_E_SBT_BELOW        = 0xEE00100B,    // point outside (below) SB_T hull found
    TASS_E_AL_NEPT        	= 0xEE00100C,    // not enough points in AL range (less than params.AL_Min)
    TASS_E_AR_NEPT        	= 0xEE00100D,    // not enough points in AR range (less than params.AR_Min)
    TASS_E_REG_LIM_AL       = 0xEE00100E,    // AL range regression line is uncertain (dR below params.AL_AR_MinR)
    TASS_E_REG_LIM_AR       = 0xEE00100F,  	// AR range regression line is uncertain (dR below params.AL_AR_MinR)
    TASS_E_REG_LIM_AM       = 0xEE001010,  	// AM range regression line is uncertain (dR below params.AM_MinR)
    // TASS limit checks
    TASS_E_M1_TOO_HIGH      = 0xEE002001,  	// M1 outside limits (too high)
    TASS_E_M2_TOO_HIGH      = 0xEE002002,  	// M2 outside limits (too high)
    TASS_E_W3_TOO_HIGH	   	= 0xEE002003,  	// W3 outside limits (too high)
    TASS_E_LA_TOO_HIGH      = 0xEE002004,  	// LA outside limits (too high)
    TASS_E_M1_TOO_LOW       = 0xEE002005,  	// M1 outside limits (too low)
    TASS_E_M2_TOO_LOW       = 0xEE002006,  	// M2 outside limits (too low)
    TASS_E_W3_TOO_LOW       = 0xEE002007,  	// W3 outside limits (too low)
    TASS_E_AM_R_TOO_HIGH    = 0xEE002008,  	// AM regression uncertain (correlation too low)
    TASS_E_AL_R_TOO_HIGH    = 0xEE002009,  	// AL regression uncertain (correlation too low)
    TASS_E_AR_R_TOO_HIGH    = 0xEE00200A,  	// AR regression uncertain (correlation too low)
}
