    getTASSResult(tassParameters: TassParameters,crv: CurveFile): Observable<TassResult> {
        let params = new TassCalculateParams
        params.curve.a = crv.angle;
        params.curve.t = crv.torque
        if (params.curve.a.length == params.curve.t.length) {
            params.curve.pointCount = params.curve.a.length
          }
        params.parameters.m1_H =    tassParameters.m1_H
        params.parameters.m1_L =    tassParameters.m1_L
        params.parameters.m1_cali = tassParameters.m1_cali
        params.parameters.m2_H =    tassParameters.m2_H
        params.parameters.m2_L =    tassParameters.m2_L
        params.parameters.m2_cali = tassParameters.m2_cali
        params.parameters.w3_H =    tassParameters.w3_H
        params.parameters.w3_L =    tassParameters.w3_L
        params.parameters.w3_cali = tassParameters.w3_cali
        params.parameters.lA_T =    tassParameters.lA_T
        params.parameters.sB_RNG =    tassParameters.sB_RNG
        params.parameters.sB_T = tassParameters.sB_T
        params.parameters.ms =    tassParameters.ms
        params.parameters.me =    tassParameters.me
        params.parameters.t1 = tassParameters.t1
        params.parameters.aL_Min =    tassParameters.aL_Min
        params.parameters.aR_Min = tassParameters.aR_Min
        params.parameters.aM_MinR =    tassParameters.aM_MinR
        params.parameters.aL_AR_MinR = tassParameters.aL_AR_MinR

        return this.http.post<TassResult>(this.baseUrl + 'Tass', params)

    }
