import { Component, Input, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { BreadcrumbsService } from '../../breadcrumbs.service';
import { BciLayoutIntl } from '../layout-intl';

@Component({
  selector: 'bci-header',
  templateUrl: './bci-header.component.html',
  styleUrls: ['./bci-header.component.scss']
})
export class BciHeaderComponent implements OnInit {

  @Input() public header = '';
  @Input() public logoRoute = '/';

  constructor(public layoutIntl: BciLayoutIntl, private router: Router, public breadcrumbService: BreadcrumbsService) {
  }

  ngOnInit() {
  }

  onLogoClicked() {
    // TODO: Cursor:pointer when logoLink or logoRoute is set
    if (this.logoRoute) {
      this.router.navigate([this.logoRoute]);
    }
  }

}
