export interface ICurveFile {
    //checkbox: boolean;
    name: string;
    state: string;
    program: number;
    channel: number;
    curveSeq: number;
    qC1: number;
    qC2: number;
    angle: number[];
    torque: number[];
}

export class CurveFile implements ICurveFile {
    //checkbox: boolean;
    name: string;
    state: string;
    program: number;
    channel: number;
    curveSeq: number;
    qC1: number;
    qC2: number;
    angle: number[];
    torque: number[];
    time: Date;
    constructor() {
      //this.checkbox = false;
      this.name = '';
      this.state = '';
      this.program = -1;
      this.channel = 0;
      this.curveSeq = 0;
      this.qC1 = 0;
      this.qC2 = 0;
      this.angle = [];
      this.torque = [];
      this.time = new Date();
    }
}
