import { Component, Input, OnChanges, OnInit, SimpleChanges } from '@angular/core';
import { BOSCH_COLORS } from '@bci-web-core/core';
import { MiddleoutGaugeCircle } from './circular.model';

type GaugeState = 'ok' | 'warn' | 'error' | 'unknown';

/**
 * Reusable component that displays a value in a half-circle gauge. It features:
 * - Current value & unit
 * - minimum and maximum value
 * - A value name/label
 * - and more (see @Inputs)
 */
@Component({
  selector: 'bci-middleout-gauge',
  templateUrl: './middleout-gauge.component.html',
  styleUrls: ['./middleout-gauge.component.scss'],
})
export class MiddleoutGaugeComponent implements OnInit, OnChanges {
  private readonly MIN_ANGLE = 0;
  private readonly MAX_ANGLE = 90;
  private readonly ARC_ROTATION_OFFSET = 90;
  private readonly GAUGE_CIRCLE = new MiddleoutGaugeCircle({ x: 200, y: 200 }, 160, this.ARC_ROTATION_OFFSET);
  private readonly INDICATOR_WIDTH = 1.4;

  /** An optional unit that is displayed next to the value */
  @Input() unit: string;
  /** The mandatory minimum value that represent the left most value that can be displayed on the chart */
  @Input() minValue = 0;
  /** The mandatory maximum value that represent the right most value that can be displayed on the chart */
  @Input() maxValue = 100;
  /** The an optional warning threshold. When value is below that threshold, the gauge is colored in yellow */
  @Input() warnThreshold: number;
  /** The an optional error threshold. When value is below that threshold, the gauge is colored in red */
  @Input() errorThreshold: number;
  /** The an optional target value that represents the value that is considered as the desired value in the given context */
  @Input() targetValue: number;
  /** The an optional string that describes the value and is displayed below the gauge */
  @Input() valueName: string;
  /** If set to false, the min and max values are not displayed on the chart. */
  @Input() displayRange = true;
  /** Number of decimal points to be displayed for the current value.  */
  @Input() digits = 1;

  targetvaluecolor: string;
  gaugeArcPath: string;
  warnThresholdArcPath: string;
  errorThresholdArcPath: string;
  targetValueArcPath: string;
  boschColors = BOSCH_COLORS;
  gaugeState: GaugeState = 'unknown';
  warnThresholdIndicatorInverted: boolean;
  errorThresholdIndicatorInverted: boolean;

  get currentValue(): number {
    return this._currentValue;
  }
  get currentValueStr(): string {
    //return currentValue.toFixed(digits);
    return this.valueFmt(this._currentValue);
  }
  get minValueStr(): string {
    return this.valueFmt(this.minValue);
  }
  get maxValueStr(): string {
    return this.valueFmt(this.maxValue);
  }
  get errorThresholdStr(): string {
    return this.valueFmt(this.errorThreshold);
  }
  get warnThresholdStr(): string {
    return this.valueFmt(this.warnThreshold);
  }

  @Input()
  set currentValue(value: number) {
    this._currentValue = value;
    this.updateGauge();
  }

  private _currentValue: number;

  ngOnInit(): void {
    this.updateGauge();
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (this.inputsChanged(changes)) {
      this.updateGauge();
    }
  }

  valueToPercentage(x: number): number {
    const valueRange = this.maxValue - this.minValue;
    const centerPoint = (valueRange/2) + this.minValue;
    const valueInRage = x - centerPoint;
    const percentage = (valueInRage / valueRange) * 200;
    return Math.max(Math.min(percentage, 100), -100);
  }

  valueToAngle(x: number): number {
    const percentage = this.valueToPercentage(x);
    return this.MIN_ANGLE + (percentage * (this.MAX_ANGLE - this.MIN_ANGLE)) / 100;
  }

// format a number to the given digis (precision)
  // Compared to the toPrecision() function, this will *not* do exponentials...
  valueFmt(x: number): string {
    if (x == null) return '';
    if (this.digits == 0) return (+x).toFixed(0);
    var s = (+x).toPrecision(this.digits);
    if (s.indexOf('e') >= 0) {
        // this can only happen if the number is too large, so only show the int part
        s = (+x).toFixed(0);
    }
    return s;
  }

  private updateGauge() {
    const valueAngle = this.valueToAngle(this._currentValue);
    const errorThresholdAngle = this.valueToAngle(this.errorThreshold);
    const warnThresholdAngle = this.valueToAngle(this.warnThreshold);
    if (this.targetValue == null) this.targetValue = (this.maxValue - this.minValue)/2;
    const targetValueAngle = this.valueToAngle(this.targetValue);

//    if (this._currentValue >= ((this.maxValue - this.minValue)/2) + this.minValue) {
//      this.gaugeArcPath = this.GAUGE_CIRCLE.toSvgArc(this.MIN_ANGLE, valueAngle);
//    }else{
//      this.gaugeArcPath = this.GAUGE_CIRCLE.toSvgArc2(this.MIN_ANGLE, valueAngle);
//    }
    if (this._currentValue >= this.targetValue) {
      // nach links
      this.gaugeArcPath = this.GAUGE_CIRCLE.toSvgArc(targetValueAngle, valueAngle);
    }else{
      // nach rechts
      this.gaugeArcPath = this.GAUGE_CIRCLE.toSvgArc2(targetValueAngle, valueAngle);
    }

    // Marker Grenzwerte links (warn) und rechts (error)
    this.warnThresholdArcPath = this.describeIndicatorArcSegment(warnThresholdAngle);
    this.errorThresholdArcPath = this.describeIndicatorArcSegment(errorThresholdAngle);

    // Zielmarker grn einfrben, falls wir nahe dran sind (+/-1.5)
    if (Math.abs(targetValueAngle - valueAngle) < 1.5) {this.targetvaluecolor = "#78BE20"}
    else{this.targetvaluecolor = "#BFC0C2"};
    this.targetValueArcPath = this.describeIndicatorArcSegment(targetValueAngle);

    this.gaugeState = this.calculateGaugeState();
    this.updateThresholdIndicators(valueAngle, warnThresholdAngle, errorThresholdAngle);
  }

  private describeIndicatorArcSegment(warnThresholdAngle: number): string {
    return this.GAUGE_CIRCLE.toSvgArc(
      warnThresholdAngle - this.INDICATOR_WIDTH / 2,
      warnThresholdAngle + this.INDICATOR_WIDTH / 2
    );
  }

  private calculateGaugeState(): GaugeState {
    let gaugeState: GaugeState = 'unknown';
    let hi = (this.errorThreshold && this._currentValue > this.errorThreshold);
    let lo = (this.warnThreshold && this._currentValue < this.warnThreshold);
    if (hi || lo) {
      gaugeState = 'error';
    } else {
      gaugeState = 'warn';
    }
    return gaugeState;
  }

  private updateThresholdIndicators(valueAngle: number, warnThresholdAngle: number, errorThresholdAngle: number): void {
    this.warnThresholdIndicatorInverted = valueAngle < warnThresholdAngle;
    this.errorThresholdIndicatorInverted = valueAngle > errorThresholdAngle;
  }

/*
  private isOk() {
    return
       (this.warnThreshold && this._currentValue >= this.warnThreshold) ||
          (this.errorThreshold && this._currentValue <= this.errorThreshold)
  }

  private isWarnThresholdExceeded(): boolean {
    return this._currentValue < this.warnThreshold;
  }

  private isErrorThresholdExceeded(): boolean {
    return this._currentValue > this.errorThreshold;
  }
*/
  private inputsChanged(changes: SimpleChanges): boolean {
    return (
      this.hasInputChanged(changes, 'unit') ||
      this.hasInputChanged(changes, 'minValue') ||
      this.hasInputChanged(changes, 'maxValue') ||
      this.hasInputChanged(changes, 'warnThreshold') ||
      this.hasInputChanged(changes, 'errorThreshold') ||
      this.hasInputChanged(changes, 'targetValue') ||
      this.hasInputChanged(changes, 'valueName') ||
      this.hasInputChanged(changes, 'displayRange') ||
      this.hasInputChanged(changes, 'digits')
    );
  }

  private hasInputChanged(changes: SimpleChanges, inputName: string): boolean {
    return changes[inputName] !== undefined && changes[inputName].currentValue !== changes[inputName].previousValue;
  }
}
