import { OverlayModule } from '@angular/cdk/overlay';
import { PortalModule } from '@angular/cdk/portal';
import { CommonModule } from '@angular/common';
import { HttpClientModule } from '@angular/common/http';
import { ModuleWithProviders, NgModule, Optional, SkipSelf } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { MAT_RIPPLE_GLOBAL_OPTIONS, RippleGlobalOptions } from '@angular/material/core';
import { MAT_FORM_FIELD_DEFAULT_OPTIONS } from '@angular/material/form-field';
import { MatFormFieldDefaultOptions } from '@angular/material/form-field/form-field';
import { MatIconRegistry } from '@angular/material/icon';
import { AlertDialogComponent } from './alert/alert-dialog/alert-dialog.component';
import { BciComponentModule } from './component.module';
import { CORE_CONFIG_URL, CoreConfigurationModule } from './core-configuration/core-configuration.module';
import { ENVIRONMENT_PROD } from './environment-prod.token';
import { BciImprintComponent } from './imprint/bci-imprint.component';
import { BCI_IMPRINT_INTL_PROVIDER } from './imprint/imprint-intl';
import { BciActivityIndicatorComponent } from './layout/bci-activity-indicator/activity-indicator.component';
import { LoadingScreenComponent } from './loading-screen/loading-screen.component';
import { BciMessagesFilterComponent } from './messages/bci-messages-filter/bci-messages-filter.component';
import { BciMessagesComponent } from './messages/bci-messages.component';
import { BCI_MESSAGES_INTL_PROVIDER } from './messages/messages-intl';
import { throwIfAlreadyLoaded } from './module-import-guard';

export interface ICoreModuleConfig {
  prod_environment?: boolean;
  core_config_url?: string;
}

export const DefaultCoreModuleConfig: ICoreModuleConfig = {
  prod_environment: false,
  core_config_url: undefined,
};

const globalRippleConfig: RippleGlobalOptions = {
  disabled: true,
  animation: {
    enterDuration: 0,
    exitDuration: 0,
  },
};

@NgModule({
  imports: [
    CommonModule,
    // TODO: needed?
    FormsModule,
    HttpClientModule,
    CoreConfigurationModule,
    /* CDK */
    OverlayModule,
    PortalModule,
    BciComponentModule,
  ],
  declarations: [],
  exports: [HttpClientModule],
  entryComponents: [
    BciImprintComponent,
    BciMessagesComponent,
    BciMessagesFilterComponent,
    AlertDialogComponent,
    LoadingScreenComponent,
    BciActivityIndicatorComponent,
  ],
  providers: [
    { provide: MAT_FORM_FIELD_DEFAULT_OPTIONS, useValue: <MatFormFieldDefaultOptions>{ floatLabel: 'always' } },
    { provide: MAT_RIPPLE_GLOBAL_OPTIONS, useValue: globalRippleConfig },
    BCI_IMPRINT_INTL_PROVIDER,
    BCI_MESSAGES_INTL_PROVIDER,
  ],
})
export class BciCoreModule {
  constructor(@Optional() @SkipSelf() parentModule: BciCoreModule, private iconRegistry: MatIconRegistry) {
    throwIfAlreadyLoaded(parentModule, 'BciCoreModule');

    iconRegistry.registerFontClassAlias('bosch', 'Bosch-Ic').setDefaultFontSetClass('Bosch-Ic');
  }

  static forRoot(config: ICoreModuleConfig): ModuleWithProviders<BciCoreModule> {
    return {
      ngModule: BciCoreModule,
      providers: [
        {
          provide: ENVIRONMENT_PROD,
          useValue: config.prod_environment || DefaultCoreModuleConfig.prod_environment,
        },
        {
          provide: CORE_CONFIG_URL,
          useValue: config.core_config_url,
        },
      ],
    };
  }
}
