import { Component, Inject, OnInit} from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { AlertService, AlertType, PrimaryButton } from '@bci-web-core/core';
import { BS300IFService } from '../../shared/backend/BS300IF-service.service';
import { Logon, ToolFile } from '../../shared/models/logonreq'
import { SelectionModel } from '@angular/cdk/collections';
import { Observable, Subject } from 'rxjs'
import { takeUntil } from 'rxjs/operators';
import { TassParameters } from '../../shared/models/tassparameters';
import { ParametersService } from '../../shared/frontend/parameter.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { marker as _ } from '@biesbjerg/ngx-translate-extract-marker';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'bci-modalconnect',
  templateUrl: './modalconnect.component.html',
  styleUrls: ['./modalconnect.component.scss']
})

export class ModalConnectComponent {

//    validationMessages = {
//      hostnameRequired: 'ConnectModal.Errors.Hostname.required',
//      rackRange: 'ConnectModal.Errors.Rack.range',
//      slotRange: 'ConnectModal.Errors.Rack.range',
//    }

    public connectionInfo: Logon = {
        Connected: false,
        Connecting: false,
        Time: "",
        LogonReq: {
            hostname: "10.10.2.244",
            rack: 0,
            slot: 1,
            username: "Bosch",
            password: "",
        },
        Parameter: {
          Path: "",
          Text: "",
          Done: 0
        },
        LogonRes: {
        components: [],
        files: [],
        firmwareVersion: ""
      }
    }
    connects$: Observable<Logon>;
    public connecting: boolean = false;
    public fileLoaded: boolean = false;
    public newlyConnected: boolean = false;
    public FileNames: ToolFile[];
    private selection: SelectionModel<ToolFile>
    private componentDestroyed$: Subject<any> = new Subject();
    public anyRowSelected: Boolean = false;
    private isErrVisible: Boolean = false;
    private _subscription;
    form: FormGroup;

  constructor(
    private dialogRef: MatDialogRef<ModalConnectComponent>,
    private alertService: AlertService,
    private BS300IFService: BS300IFService,
    private parameterService: ParametersService,
    private formBuilder: FormBuilder,
    private translate: TranslateService,

    @Inject(MAT_DIALOG_DATA) public data: any
  ) {
    console.log("ModalConnect()");
    this.form = this.formBuilder.group({
      hostname: ['', [Validators.required, Validators.minLength(3)] ],
      rack: ['', [Validators.required, Validators.min(0), Validators.max(15)] ],
      slot: ['', [Validators.required, Validators.min(1), Validators.max(6)] ],
      username: ['', Validators.required],
      password: ['']
    });
  }

  ngOnInit(): void {
    console.log("ModalConnect.OnInit()");
    this.restoreFormData();
    this.BS300IFService.CurveTimer_Stop();
    this.connectionInfo.Parameter.Done = 0;
    this.fileLoaded = false;
    this.selection = new SelectionModel<ToolFile>(false, []);
    this.selection.changed.pipe(takeUntil(this.componentDestroyed$)).subscribe(change => {
      this.anyRowSelected = change.source.hasValue();
      //this.singleRowSelected = change.source.selected.length === 1;
      //this.updateItemCount();
    });
    this.anyRowSelected = false;
    this.connecting = false;
    this.newlyConnected = false;
    this.connects$ = this.BS300IFService.connects$;
    this._subscription = this.connects$.subscribe((connect: Logon) => {
      const connectStep = this.BS300IFService.GetConnectStep();
      console.log("ModalConnect.connect$! - step: " + connectStep);
      if (true) {
          this.connectionInfo = connect; //connect[connect.length-1]
          this.FileNames = this.connectionInfo.LogonRes.files;
          this.newlyConnected = this.connectionInfo.Connected;
          if (this.newlyConnected)
            this.connecting = false;
          if (connectStep == 2 && connect.Connected == false) {
            const ref = this.alertService.openAlertBox({
              title: this.translate.instant(_('Alert.ConnectError.title')),
              description: this.translate.instant(_('Alert.ConnectError.description')),
              type: AlertType.Warning,
              leftButtonTitle: '',
              rightButtonTitle: 'Ok',
              primaryButton: PrimaryButton.RightButton
            });
            ref.afterClosed().subscribe(res => {
              if (res === 'right') {
                //this.dialogRef.close();
              }
            });
          }
          if (connectStep > 2) {
            // file download completed
            const success = (connectStep > 3);
            //this.connectionInfo.Parameter != null && this.connectionInfo.Parameter.Done != null && this.connectionInfo.Parameter.Done != 0) {
            if (!success) { //connectStep == 3 && this.connectionInfo.Parameter.Done < 0) {
                // some error!
                this.connectionInfo.Parameter.Done = 0;
                if (!this.isErrVisible) {
                  this.isErrVisible = true;
                  const ref = this.alertService.openAlertBox({
                    title: this.translate.instant(_('Alert.DownloadFileError.title')),
                    description: this.translate.instant(_('Alert.DownloadFileError.description')),
                    type: AlertType.Warning,
                    leftButtonTitle: '',
                    rightButtonTitle: 'Ok',
                    primaryButton: PrimaryButton.RightButton
                  });
                  ref.afterClosed().subscribe(res => {
                    if (res === 'right') {
                      this.isErrVisible = false;
                      //this.dialogRef.close();
                    }
                  });
                }
              }
              else {
                // todo: update the parameter object (eventually using the parameter manager?)
                var p: TassParameters;
                p = this.parameterService.fromText(this.connectionInfo.Parameter.Text, this.connectionInfo.Parameter.Path);
                //this.parameterService.create(Object.assign({},Object.assign(Object.assign({},p), p)));
                if (p == null || p.valid == null || !p.valid) {
                  this.connectionInfo.Parameter.Done = 0;
                  this.fileLoaded = false;
                  if (!this.isErrVisible) {
                    this.isErrVisible = true;
                    const ref = this.alertService.openAlertBox({
                      title: this.translate.instant(_('Alert.DownloadFileInvalid.title')),
                      description: this.translate.instant(_('Alert.DownloadFileInvalid.description')),
                      type: AlertType.Warning,
                      leftButtonTitle: '',
                      rightButtonTitle: 'Ok',
                      primaryButton: PrimaryButton.RightButton
                    });
                    ref.afterClosed().subscribe(res => {
                      if (res === 'right') {
                        this.isErrVisible = false;
                        //this.dialogRef.close();
                      }
                    });
                  }
                }
                else {
                  // use the newly received parameters
                  this.parameterService.publish(p);
                  // close this dialog and return to the caller.
                  this.BS300IFService.SetConnectStep(2);
                  this.connectionInfo.Parameter.Done = 0;
                  this.fileLoaded = true;
                  //this.close();
                }
              }
            }
        }
    })
  }

  // Initialize form fields with default (or stored) data.
  restoreFormData() {
    this.form.patchValue({
      hostname: localStorage.getItem('TASS.connection.hostname'),
      rack: localStorage.getItem('TASS.connection.rack') ?? 0,
      slot: localStorage.getItem('TASS.connection.slot') ?? 1,
      username: localStorage.getItem('TASS.connection.username') ?? 'Bosch'
    });
  }
  saveFormData() {
    if (this.form.valid) {
      // only save data, if the form is valid!
      var tmo = this.form.get('hostname')
      localStorage.setItem('TASS.connection.hostname', this.form.get('hostname').value);
      localStorage.setItem('TASS.connection.rack', this.form.get('rack').value);
      localStorage.setItem('TASS.connection.slot', this.form.get('slot').value);
      localStorage.setItem('TASS.connection.username', this.form.get('username').value);
    }
  }
  // provide accessors to form fields (for validation in the template)
  get ctlHostname() { return this.form.get('hostname')!; }
  get ctlRack() { return this.form.get('rack')!; }
  get ctlSlot() { return this.form.get('slot')!; }
  get ctlUsername() { return this.form.get('username')!; }
  get ctlPassword() { return this.form.get('password')!; }


  // download a tass parameter file
  download(): void {
    if (this.anyRowSelected) {
      var last_selected = this.selection.selected[this.selection.selected.length - 1].name;
      this.BS300IFService.downloadParamFile(last_selected);
    }
  }

  close(): void {
    this._subscription.unsubscribe();
    this.dialogRef.close();
    if (!this.connectionInfo.Connected) {
      this.newlyConnected = false;
      this.connecting = false;
      this.connectionInfo.Connecting = false;
    } else {
      // we are connected, so start the curve timer
      this.BS300IFService.CurveTimer_Start();
    }
    this.saveFormData();
    console.log('Closed by backdrop or icon');
  }

  delete(): void {
    const ref = this.alertService.openAlertBox({
      title: 'Delete Item?',
      description: 'This action will delete this item.',
      type: AlertType.Warning,
      leftButtonTitle: 'Delete',
      rightButtonTitle: 'Cancel',
      primaryButton: PrimaryButton.RightButton
    });

    ref.afterClosed().subscribe(res => {
      if (res === 'left') {
        console.log('Delete Item');
        this.dialogRef.close();
      }
    });
  }

  connect(): void {
    this.connecting = true;
    this.newlyConnected = false;
    // get the form data values into out internal data structure...
    this.connectionInfo.LogonReq = {
      hostname: this.form.get('hostname').value,
      rack: +this.form.get('rack').value,
      slot: +this.form.get('slot').value,
      username: this.form.get('username').value,
      password: this.form.get('password').value,
    };
    this.BS300IFService.connect(this.connectionInfo)
    //this.dialogRef.close();
  }

  disconnect(): void {
    this.newlyConnected = false;
    this.connecting = false;
    this.connectionInfo.Connecting = false;
    this.connectionInfo.Connected = false;
    const ref = this.alertService.openAlertBox({
      title: this.translate.instant(_('Alert.Disconnect.title')),
      description: this.translate.instant(_('Alert.Disconnect.description')),
      type: AlertType.Warning,
      leftButtonTitle: this.translate.instant(_('Disconnect')),
      rightButtonTitle: this.translate.instant(_('Cancel')),
      primaryButton: PrimaryButton.RightButton
    });

    ref.afterClosed().subscribe(res => {
      if (res === 'left') {
          this.BS300IFService.disconnect()
          this.dialogRef.close();
          this._subscription.unsubscribe();
      }
    });
  }

  cancel(): void {
    if (this.BS300IFService.GetConnectStep() >= 2) {
      this.BS300IFService.SetConnectStep(2);
    }
    const ref = this.alertService.openAlertBox({
      title: this.translate.instant(_('Alert.Close.title')),
      description: this.translate.instant(_('Alert.Close.description')),
      type: AlertType.Warning,
      leftButtonTitle: this.translate.instant(_('Yes')),
      rightButtonTitle: this.translate.instant(_('No')),
      primaryButton: PrimaryButton.RightButton
    });

    ref.afterClosed().subscribe(res => {
      if (res === 'left') {
        this.dialogRef.close();
        this._subscription.unsubscribe();
      }
    });
    this.saveFormData();
  }

}
