import { FocusMonitor } from '@angular/cdk/a11y';
import { COMMA, ENTER } from '@angular/cdk/keycodes';
import {
  Component,
  ElementRef,
  EventEmitter,
  HostBinding,
  Input,
  OnDestroy,
  OnInit,
  Output,
  ViewChild,
} from '@angular/core';
import { FormControl, NgControl, ValidationErrors } from '@angular/forms';
import { MatAutocompleteSelectedEvent, MatAutocompleteTrigger } from '@angular/material/autocomplete';
import { MatChipInputEvent } from '@angular/material/chips';
import { MatFormFieldControl } from '@angular/material/form-field';
import { Observable, of, Subject } from 'rxjs';
import { map, mergeMap, startWith, tap } from 'rxjs/operators';
import { ChipOption } from './chip-option.model';

export type FilterableChipOptionProvider = (userInput: string | null) => Observable<ChipOption[]>;

@Component({
  selector: 'bci-chip-list-autocomplete',
  templateUrl: './chip-list-autocomplete.component.html',
  styleUrls: ['./chip-list-autocomplete.component.scss'],
  providers: [{ provide: MatFormFieldControl, useExisting: BciChipListAutocompleteComponent }],
})
export class BciChipListAutocompleteComponent implements MatFormFieldControl<ChipOption[]>, OnInit, OnDestroy {
  static nextId = 0;

  separatorKeysCodes: number[] = [ENTER, COMMA];

  readonly stateChanges: Subject<void> = new Subject();

  @HostBinding()
  id = `chip-list-input-autocomplete-${BciChipListAutocompleteComponent.nextId++}`;

  @HostBinding('attr.aria-describedby')
  describedBy = '';
  pendingFreeTextInput = false;
  availableOptions: ChipOption[] = [];
  @Output()
  valueChange = new EventEmitter<ChipOption[]>();
  userInputControl: FormControl = new FormControl();
  userFilteredOptions: Observable<ChipOption[]>;
  @Input()
  inlineStyle = true;
  focused = false;
  touched = false;
  controlType = 'chip-list-input-autocomplete';
  @ViewChild('userInput', { static: false })
  userInput: ElementRef<HTMLInputElement>;
  @ViewChild(MatAutocompleteTrigger, { static: false })
  autocompleteTrigger: MatAutocompleteTrigger;
  readonly ngControl: NgControl = null;
  private _placeholder: string;
  private _optionsProvider: FilterableChipOptionProvider = this.buildStaticOptionsProvider();
  private _options: ChipOption[] = [];
  private _value: ChipOption[] = [];
  private _externalFreeTextValidator: (userInput: string) => ValidationErrors;
  private _allowFreeText = false;
  private _required = false;
  private _disabled = false;
  private _chipLimit: number = undefined;

  constructor(private focusMonitor: FocusMonitor, private htmlElementRef: ElementRef<HTMLElement>) {
    focusMonitor.monitor(this.htmlElementRef.nativeElement, true).subscribe((origin) => {
      this.touched = this.touched || !origin;
      this.focused = !!origin;
      this.stateChanges.next();
    });
  }

  get placeholder(): string {
    return this._placeholder;
  }

  @Input()
  set placeholder(value: string) {
    this._placeholder = value;
    this.stateChanges.next();
  }

  get optionsProvider(): FilterableChipOptionProvider {
    return this._optionsProvider;
  }

  @Input()
  set optionsProvider(optionsProvider: FilterableChipOptionProvider) {
    if (optionsProvider && this._options && this._options.length > 0) {
      throw new Error(
        'Initialising failed. When [optionsProvider] input is set, you cant pass static options via [options].'
      );
    }

    if (!optionsProvider) {
      this._optionsProvider = this.buildStaticOptionsProvider();
    } else {
      this._optionsProvider = optionsProvider;
    }
    this.stateChanges.next();
  }

  get options(): ChipOption[] {
    return this._options;
  }

  @Input('options')
  set options(staticOptions: ChipOption[]) {
    if (staticOptions) {
      this._options = staticOptions;
      this.availableOptions = this.filterAvailableOptions(staticOptions);
      this._optionsProvider = this.buildStaticOptionsProvider();
    } else {
      this._options = [];
      this.availableOptions = [];
    }
    this.resetUserInput();
    this.stateChanges.next();
  }

  @Input('value')
  get value(): ChipOption[] {
    return this._value;
  }

  set value(value: ChipOption[]) {
    if (value) {
      this._value = value.slice();
      this.availableOptions = this.filterAvailableOptions(this._options);
    } else {
      this._value = undefined;
    }
    this.resetUserInput();
    this.stateChanges.next();
  }

  get empty(): boolean {
    return (!this._value || this._value.length === 0) && this.inputEmpty;
  }

  get hasChips(): boolean {
    return this._value && this._value.length > 0;
  }

  get inputEmpty(): boolean {
    return !this.userInputControl.value;
  }

  get externalFreeTextValidator(): (userInput: string) => ValidationErrors {
    return this._externalFreeTextValidator;
  }

  @Input()
  set externalFreeTextValidator(value: (userInput: string) => ValidationErrors) {
    this._externalFreeTextValidator = value;
    this.stateChanges.next();
  }

  get allowFreeText(): boolean {
    return this._allowFreeText;
  }

  @Input()
  set allowFreeText(value: boolean) {
    this._allowFreeText = value;
    this.stateChanges.next();
  }

  @HostBinding('class.floating')
  get shouldLabelFloat() {
    return this.focused || !this.empty;
  }

  get errorState(): boolean {
    return this.userInputControl.hasError('externalError') || (this.touched && !!this.userInputControl.errors);
  }

  get required() {
    return this._required;
  }

  @Input()
  set required(req: boolean) {
    this._required = req;
    this.stateChanges.next();
  }

  get disabled(): boolean {
    return this._disabled;
  }

  @Input()
  set disabled(value: boolean) {
    this._disabled = value;
    this._disabled ? this.userInputControl.disable() : this.userInputControl.enable();
    this.stateChanges.next();
  }

  get chipLimit(): number {
    return this._chipLimit;
  }

  @Input()
  set chipLimit(value: number) {
    this._chipLimit = value;
    this.stateChanges.next();
  }

  ngOnInit() {
    this.userInputControl.setValidators([
      (control: FormControl) => this.validateUserInputIsValidOption(control.value),
      (control: FormControl) => this.validateFreeTextWithExternalValidator(control.value),
      (control: FormControl) => this.validatePendingTextInput(control.value),
      (control: FormControl) => this.validateChipListNotEmpty(control.value),
    ]);

    this.userFilteredOptions = this.userInputControl.valueChanges.pipe(
      startWith(''),
      mergeMap((userInput) => {
        return this._optionsProvider(userInput);
      }),
      map((resolvedChipOptions) => this.filterAvailableOptions(resolvedChipOptions)),
      tap((filteredChipOptions) => {
        this.availableOptions = filteredChipOptions;
        this.stateChanges.next();
      })
    );
  }

  ngOnDestroy() {
    this.stateChanges.next();
    this.focusMonitor.stopMonitoring(this.htmlElementRef.nativeElement);
  }

  setDescribedByIds(ids: string[]) {
    this.describedBy = ids.join(' ');
  }

  onContainerClick(event: MouseEvent): void {
    this.htmlElementRef.nativeElement.querySelector('input').focus();
  }

  onAutocompleteOptionSelected(event: MatAutocompleteSelectedEvent): void {
    if (!this.isChipLimitReached()) {
      this._value.push(this.availableOptions.find((option) => option.id === event.option.id));

      this.resetUserInput();
      this.valueChange.emit(this._value);
      this.stateChanges.next();
    }
  }

  isChipLimitReached() {
    return !!this._chipLimit && this.value.length >= this._chipLimit;
  }

  onSelectedOptionRemoved(optionId: string): void {
    const index = this._value.findIndex((el) => el.id === optionId);

    if (index >= 0) {
      this._value.splice(index, 1);
    }

    this.resetUserInput();
    this.valueChange.emit(this._value);
    this.stateChanges.next();
  }

  addFreeTextChip(event: MatChipInputEvent) {
    if (!this.isChipLimitReached() && this._allowFreeText && !this.userInputControl.hasError('externalError')) {
      const value = event.value;

      if (value && value.trim()) {
        const freeTextinput = value.trim();

        const existingOption = this.availableOptions.find((option) => option.name === freeTextinput);
        const newChip = existingOption
          ? existingOption
          : {
              id: freeTextinput,
              name: freeTextinput,
            };

        this._value.push(newChip);
        this.resetUserInput();
        this.valueChange.emit(this._value);
        this.stateChanges.next();
      }
    }
  }

  onClick($event: MouseEvent) {
    this.autocompleteTrigger.openPanel();
  }

  private buildStaticOptionsProvider(): FilterableChipOptionProvider {
    return (userInput: string) => of(this.filterAvailablePredefinedOptions(userInput));
  }

  private validateUserInputIsValidOption(userInput: string): ValidationErrors | null {
    let errors: ValidationErrors = {};
    const userInputIsValidOption = this.availableOptions.map((option) => option.name).indexOf(userInput) !== -1;

    if (!!userInput && !this.allowFreeText && !userInputIsValidOption) {
      errors.requireMatch = true;
    } else {
      errors = null;
    }
    return errors;
  }

  private validateFreeTextWithExternalValidator(userInput: string): ValidationErrors | null {
    let errors: ValidationErrors = null;
    if (!!userInput && this.allowFreeText && this.externalFreeTextValidator) {
      errors = this.externalFreeTextValidator(userInput);
      if (errors) {
        errors.externalError = true;
      }
    }
    return errors;
  }

  private validatePendingTextInput(userInput: string): null {
    this.pendingFreeTextInput = !!userInput;
    return null;
  }

  private validateChipListNotEmpty(userInput: string): ValidationErrors | null {
    let errors: ValidationErrors = {};
    if (this.required && !userInput && !!this._value && this._value.length === 0) {
      errors.required = true;
    } else {
      errors = null;
    }
    return errors;
  }

  private resetUserInput() {
    if (this.userInput) {
      this.userInput.nativeElement.value = '';
    }
    this.userInputControl.setValue('');
  }

  private filterAvailableOptions(allOptions: ChipOption[]): ChipOption[] {
    let nonSelectedOptions: ChipOption[];
    if (!allOptions) {
      nonSelectedOptions = [];
    } else if (!this._value) {
      nonSelectedOptions = allOptions;
    } else {
      nonSelectedOptions = allOptions.filter(
        (chipOption) => this._value.map((selOption) => selOption.id).indexOf(chipOption.id) === -1
      );
    }
    return nonSelectedOptions;
  }

  private filterAvailablePredefinedOptions(filter: string): ChipOption[] {
    if (filter) {
      return this._options.filter(this.byName(filter));
    } else {
      return this._options.slice();
    }
  }

  private byName(filter: string): (chipOption: ChipOption) => boolean {
    return (availableOption) => availableOption.name.toLowerCase().indexOf(filter.toLowerCase()) !== -1;
  }
}
