import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { ModalWindowService } from '../modal-window/modal-window.service';
import { BciImprintDetailsService } from './bci-imprint-details.service';
import { BciImprintIntl } from './imprint-intl';
import { ImprintModel } from './imprint.model';
import { ElectronService } from 'ngx-electron';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'bci-imprint',
  templateUrl: './bci-imprint.component.html',
  styleUrls: ['./bci-imprint.component.scss']
})
export class BciImprintComponent implements OnInit {
  data: ImprintModel;
  versions = {
    //node: '',
    chrome: '',
    electron: '',
    tassdll: '',
    bs300if: '',
  };

  constructor(public imprintIntl: BciImprintIntl,
              private modalWindowService: ModalWindowService,
              private httpClient: HttpClient,
              private imprintDetailsService: BciImprintDetailsService,
              private electronService: ElectronService,
              private dialogRef: MatDialogRef<BciImprintComponent>) {

    const chrome = navigator.userAgent.match(/Chrome\/\S*/);
    const electron = navigator.userAgent.match(/Electron\/\S*/);
    this.versions.chrome = chrome ? chrome[0] : '???';
    this.versions.electron = electron ? electron[0] : '(not running in electron)';
/*    if (electronService.isElectronApp) {
        this.versions = electronService.process.versions;
      }*/
  }

  ngOnInit() {
    this.imprintDetailsService.getImprintDetails().subscribe(
      imprintModel => {
        this.data = imprintModel;
      }
    );
    this.httpClient.get('/api/info/dllversions').subscribe(
      data => {
        console.log(data);
        //@ts-ignore
        this.versions.bs300if = data.bs300if.productVersion;
        //@ts-ignore
        this.versions.tassdll = data.tass.productVersion;
      }
    );
  }


  public clickCancel(): void {
    this.dialogRef.close();
  }

}
