import { Injectable } from '@angular/core';
import { LangChangeEvent, TranslateService } from '@ngx-translate/core';
import { marker as _ } from '@biesbjerg/ngx-translate-extract-marker';

@Injectable()
export class BciImprintIntl {

  /* A title for imprint dialog */
  title = 'Imprint';

  /* Alternative label for Bosch logo */
  boschLogoAlt = 'Bosch Invented For Life Logo';

  /* A label for the application name */
  name = 'Name';

  /* A label for the application version */
  version = 'Version';

  /* A label for the company name and address section */
  nameAndAddress = 'Name and address';

  /* A label for the contact section */
  contact = 'Contact';

  /* A label for the board of management section */
  board = 'Board of management';

  constructor(
    private translate: TranslateService
  ) {
    this.translate.onLangChange.subscribe((event: LangChangeEvent) => {
      //this.translate.get('Imprint.title').subscribe((translated: string) => {
        //console.log(res);      //=> 'Hello world'
        this.title = this.translate.instant(_('Imprint.title'));
        this.name = this.translate.instant(_('Imprint.name'));
        this.version = this.translate.instant(_('Imprint.version'));
        this.nameAndAddress = this.translate.instant(_('Imprint.nameAndAddress'));
        this.contact = this.translate.instant(_('Imprint.contact'));
        this.board = this.translate.instant(_('Imprint.board'));
      //});
    });
  }
}

export const BCI_IMPRINT_INTL_PROVIDER = {
  provide: BciImprintIntl,
  useClass: BciImprintIntl
};
