import { Component } from "@angular/core";
import { TranslateService } from '@ngx-translate/core';
import { OnInit } from "@angular/core";
import { AppsettingsService } from "../shared/appsettings.service";


@Component({
    selector: 'bci-einstellungen',
    templateUrl: 'einstellungen.component.html',
    styleUrls: ['einstellungen.component.scss']
  })

export class EinstellungenComponent implements OnInit {

    constructor(
      private translate: TranslateService,
      private appSettings: AppsettingsService
      )
    {
    }

    public get currentLanguage(): string { return this.appSettings.currentLanguage; };
    public set currentLanguage(v: string) { this.appSettings.currentLanguage = v; };
    public get decimalSeperator(): string { return this.appSettings.decimalSeperator };
    public set decimalSeperator(v: string) { this.appSettings.decimalSeperator = v; };
    public get fieldSeperator(): string { return this.appSettings.fieldSeperator };
    public set fieldSeperator(v: string) { this.appSettings.fieldSeperator = v; };

    ngOnInit(): void {
    }

    changeLanguage(language: string) {
      this.appSettings.currentLanguage = language;
    }

}
