import { Component, EventEmitter, Input, Output } from '@angular/core';
import { CalloutType } from './callout.model';


@Component({
  selector: 'bci-callout',
  templateUrl: './callout.component.html',
  styleUrls: ['./callout.component.scss']
})
export class CalloutComponent {
  /** The type of the callout */
  @Input() type: CalloutType;

  /** The content to be displayed */
  @Input() content: string;

  /** Flag indicates if callout is closable */
  @Input() isClosable = false;

  /** Flag indicates if icon is visible */
  @Input() isIconVisible = true;

  /** Event emits when callout is closed */
  @Output() closeClicked = new EventEmitter();

  onClose(): void {
    this.closeClicked.emit();
  }

  getCalloutIcon(): string {
    switch (this.type) {
      case 'success':
        return 'Bosch-Ic-notification-success';
      case 'info':
        return 'Bosch-Ic-notification-hint';
      case 'warn':
        return 'Bosch-Ic-notification-warning';
      case 'danger':
        return 'Bosch-Ic-notification-error';
      default:
        return '';
    }
  }
}
