import { Component, OnInit, OnDestroy, AfterViewInit, ElementRef, ViewChild } from '@angular/core';
import { Subscription, BehaviorSubject, Observable, Subject, fromEvent } from 'rxjs';
import { debounceTime, takeUntil } from 'rxjs/operators';
import { BciLoaderService, LoaderQueueElement, ModalWindowService } from '@bci-web-core/core';

import { ParametersService } from '../shared/frontend/parameter.service';
import { CurveFileService } from '../shared/frontend/curvefile-service.service';
import { BS300IFService } from '../shared/backend/BS300IF-service.service';

import { TranslateService } from '@ngx-translate/core';
import { TassViewDataService } from '../shared/frontend/tassviewdataservice';
import { TassViewData, TassViewUpdateData, TassViewEventType, TassViewResultType } from "../shared/models/tassviewdata";
import { TassParameters } from '../shared/models/tassparameters';
import { AnalyseData } from '../shared/models/analyse_data';
import { ModalExportComponent } from './modalexport/modalexport.component';
import { MatDialog } from '@angular/material/dialog';
import { Logon } from '../shared/models/logonreq';
import { AnaylseChartComponent } from './anaylse-chart/anaylse-chart.component';
import { Router } from '@angular/router';
import ResizeObserver from 'resize-observer-polyfill';

@Component({
    selector: 'bci-analyse',
    templateUrl: 'analyse.component.html',
    styleUrls: ['analyse.component.scss']
  })
  export class AnalyseComponent implements OnInit, OnDestroy, AfterViewInit {

  // Handle window resize event to fix resizing issues of the <bci-table-container>
  @ViewChild('refVerzeichnis') tableContainer: ElementRef;  // <bci-table-container> element
  @ViewChild('refChartM1') refChartM1: AnaylseChartComponent;
  @ViewChild('refChartM2') refChartM2: AnaylseChartComponent;
  @ViewChild('refChartW3') refChartW3: AnaylseChartComponent;
  @ViewChild('refChartLA') refChartLA: AnaylseChartComponent;
  resizeObservable$: Observable<Event>;
  resizeSubscription$: Subscription;
  layoutObservable$: Observable<Event>;
  layoutSubscription$: Subscription;

  private componentDestroyed$: Subject<any> = new Subject();

  CurveFiles: TassViewData[] = [];

  paramObservable$: Observable<TassParameters>;
  paramSubscription$: Subscription;
  tassParams: TassParameters = {};
  selectIdx: number = null;
  fullscreen: boolean[] = [ false, false, false, false ];
  maxChartWidth: number = null;

  dataM1: AnalyseData = new AnalyseData();
  dataM2: AnalyseData = new AnalyseData();
  dataLA: AnalyseData = new AnalyseData();
  dataW3: AnalyseData = new AnalyseData();
  oldWidth: number = 0;
  oldHeight: number = 0;

  t1Index: number = -1;
  t1Loader: LoaderQueueElement = null;

  tassViewDataSubscription$: Subscription;

  connectObservable$: Observable<Logon>;
  connectSubscription$: Subscription;
  public connectionInfo: Logon = new Logon();

//-----------------------------------------------------------------------------------------------
  constructor (
      private _matDialog: MatDialog,
      private parametersService: ParametersService,
      private curveService: CurveFileService,
      private tassViewDataService: TassViewDataService,
      private BS300IFService: BS300IFService,
      private _modalWindowService: ModalWindowService,
      private router: Router,
      private loaderService: BciLoaderService,
      private elRef: ElementRef, // hack to get access to the dom (this element)
      )
      {

      }
//-----------------------------------------------------------------------------------------------
  ngOnInit(): void
  {
      this.paramObservable$ = this.parametersService.params$;
      this.paramSubscription$ = this.paramObservable$.subscribe((param: TassParameters) => {
        console.log('AnalyseComp::OnParametersChanged: ', param);
        //this.tassParams = param;        // LocalParams
        this.tassParams = this.parametersService.getEditParams();
      });
      this.tassViewDataSubscription$ = this.tassViewDataService.items$.subscribe((item: TassViewUpdateData) => {
        console.log('AnalyseComp::OnTassViewDataChange: ', item);
        // something has changed in our curve list - copy it for view here
        this.CurveFiles = item.items;
        if (this.t1Index >= 0) {
          // we are scanning...
          console.log("(scanning) -> " + this.t1Index); // + ': ' + trd.curve.name);
          if (!this.t1Next()) {
            // finished scanning
            if (this.t1Loader) {
              this.loaderService.hideProgressBar(this.t1Loader);
            }
            // recalculate stats and redraw
            this.recalculate()
          }
          return;
        }

        // recalculate stats and redraw eventually
        this.recalculate()
      });
      this.connectObservable$ = this.BS300IFService.connects$;
      this.connectSubscription$ = this.connectObservable$.subscribe((connect: Logon) => {
        console.log('AnalyseComp::OnConnectChange: ', connect);
        this.connectionInfo = connect;
      });
      // Show the progress bar to block user input until we are sure, that
      // we don't need (or we do need) recalculations...
      this.t1Loader = this.loaderService.showProgressBar();
  }
//-----------------------------------------------------------------------------------------------
  ngAfterViewInit() {
    // Handle resizing the layout - attach a ResizeObserver
    let layoutContainer = this.elRef.nativeElement.parentElement
    console.log("AnalyseComp container ------->", layoutContainer);
    var ro = new ResizeObserver(entries => {
      for (let entry of entries) {
        const cr = entry.contentRect;
        //console.log('Element:', entry.target);
        //console.log(`Element padding: ${cr.top}px ; ${cr.left}px`);
        this.RecalculateAndFixLayout(cr.width, cr.height);
      }
    });
    ro.observe(layoutContainer);

    // Handle resizing the Browser/Electron window
    this.resizeObservable$ = fromEvent(window, 'resize')
    this.resizeSubscription$ = this.resizeObservable$.pipe(debounceTime(200)).subscribe( evt => {
      console.log('AnalyseComp::OnResize: ', evt);
      //@ts-ignore
      console.log('  new windows height:', window.innerHeight);
      //@ts-ignore
      console.log('event: new window.innerHeight', evt.target.innerHeight);
      //this.HandleWindowResize(evt.target.innerWidth, evt.target.innerHeight);
      this.HandleWindowResize(window.innerWidth, window.innerHeight);
    });
    try {
      this.tableContainer.nativeElement.style.maxHeight = (window.innerHeight - 148) + "px";
    } catch {}

    //@ts-ignore
    if (typeof mainWindow != 'undefined' && mainWindow != null) {
      //@ts-ignore
      mainWindow.on("resize", function() {
        console.log('AnalyseComp::WindowResize');
        //@ts-ignore
        var size = mainWindow.getSize();
        this.HandleWindowResize(size[0], size[1]);
      });
    } else {
    }

    // Check, if a recalculation is needed - if so, start it here!
    console.log('AnalyseComp::AfterViewInit: Check if recalc needed!');
    let dirtyCount: number = 0;
    this.CurveFiles.forEach((v) => {
      if (v.res?.isDirty != false) dirtyCount++;
    });
    if (dirtyCount == 0) {
      this.loaderService.hideProgressBar(this.t1Loader);
    } else {
      this.t1StartRecalculate();
    }
  }
//-----------------------------------------------------------------------------------------------
  ngOnDestroy() {
    this.connectSubscription$.unsubscribe();
    this.tassViewDataSubscription$.unsubscribe();
    this.paramSubscription$.unsubscribe();
    this.resizeSubscription$.unsubscribe();
  }

  private RecalculateAndFixLayout(containerWidth: number, containerHeight: number) {
    let idxFullscreen = -1
    this.fullscreen.forEach((e, i) => {
      if (e) idxFullscreen = i;
    });
    let wList = containerWidth * 0.30;   // calculate minmax(300px, 30%)
    if (wList < 300) wList = 300;        // lower limit is 300px
    let w = containerWidth - wList;      // grid cols are approx. 1fr 1fr minmax(300px, 30%)
    if (idxFullscreen >= 0) {
      let s = (w - 5);              // add a little space
      this.maxChartWidth = s - 40;  // side panel with buttons
    } else {
      let s = (w - 5) / 2;          // add a little space and divide by two
      this.maxChartWidth = s - 40;  // side panel with buttons
    }
    // Also force the stupid <bci-table-container> to resize correctly!
    this.tableContainer.nativeElement.style.maxWidth = (wList - 2) + "px";
    console.log(`AnaylseComp::container size: ${containerWidth}px x ${containerHeight}px, ChartWidth: ${this.maxChartWidth}px`);
  }
  private HandleWindowResize(newWidth: number, newHeight: number): void {
    // Hack for CSS layout issues
    try {
      // fix the height of the <bci-mat-table>
      if (this.tableContainer != null) {
        console.log('   -->:', this.tableContainer.nativeElement.offsetHeight);
        ////@ts-ignore
        //this.txtTmp = "H:" +this.tableContainer.nativeElement.style.maxHeight + "/" + (newHeight - 410) + "px";
        //@ts-ignore
        this.tableContainer.nativeElement.style.maxHeight = (newHeight - 148) + "px";
      }
    }
    catch {}
    // To workaround more bugs: fore a reload whenever the layout shrinks!
    if (newHeight < this.oldHeight || newWidth < this.oldWidth) {
      this.ForceComponentReload();
    }
    this.oldHeight = newHeight;
    this.oldWidth = newWidth;
  }
  private ForceComponentReload(): void {
    // reload the component - fix CSS layout issues with current chrome
    let currentUrl = this.router.url;
    this.router.routeReuseStrategy.shouldReuseRoute = () => false;
    this.router.onSameUrlNavigation = 'reload';
    this.router.navigate([currentUrl]);
  }

  ///! recalculate TASS for all curves
  private t1StartRecalculate()
  {
    this.t1Index = -1;
    if (this.t1Next()) {
      console.log("(scanning)");
    }
  }
  private t1Next(): boolean {
    this.t1Index++;
    for (; this.t1Index < this.CurveFiles.length; this.t1Index++) {
      if (this.CurveFiles[this.t1Index].res?.isDirty != false) {
        // update/overwrite the edit parames (so save and export will have these later)
        this.CurveFiles[this.t1Index].par = this.tassParams;
        // start calculation
        this.tassViewDataService.calculateItem(this.CurveFiles[this.t1Index], TassViewEventType.ItemChange);
        return true;  // possibly more left...
      }
    }
    this.t1Index = -1;
    return false; // no more curves left
  }


  // Set population to false, if you have only sample values
  private calcstdev(data: AnalyseData, isPopulation: boolean)
  {
    // sum/average
    const d = data.y.data;
    if (d.length == 0) {
      data.y.avg = null;
      data.y.dev = null;
      data.y.cmt = null;
      data.y.cmkt = null;
      return;
    }
    const sum = d.reduce((acc, val) => (val.measurementOk != null) ? acc + val.y : acc, 0);
    const cnt = d.reduce((acc, val) => (val.y != null && val.measurementOk != null) ? acc+1 : acc, 0);
    data.y.avg = sum / cnt;
/*  // stddev
    const sqsums = d.map(val => (val.y - data.y.avg) ** 2);
    const sqsum = sqsums.reduce((a, b) => a + b);
*/  // calculate, but skip empty values
    let sqsum = 0;
    d.forEach((val) => {
      if (val.y != null && val.measurementOk != null) {
        sqsum += (val.y - data.y.avg) ** 2;
      }
    });
    data.y.dev = Math.sqrt(sqsum / (cnt - (isPopulation ? 0 : 1)));
    if (data.y.dev > 0) {
      data.y.cmt = (data.y.max - data.y.min) / (6.0 * data.y.dev);
      let cmo = (data.y.max - data.y.avg) / (3.0 * data.y.dev);
      let cmu = (data.y.avg - data.y.min) / (3.0 * data.y.dev);
      data.y.cmkt = (cmo < cmu) ? cmo : cmu;
    } else {
      data.y.cmt = 0;
      data.y.cmkt = 0;
    }
  }

  private recalculate()
  {
    this.dataM1 = new AnalyseData();
    this.dataM1.y.cali = this.tassParams.m1_cali;
    this.dataM1.y.min = this.tassParams.m1_L;
    this.dataM1.y.max = this.tassParams.m1_H;
    this.dataM2 = new AnalyseData();
    this.dataM2.y.cali = this.tassParams.m2_cali;
    this.dataM2.y.min = this.tassParams.m2_L;
    this.dataM2.y.max = this.tassParams.m2_H;
    this.dataW3 = new AnalyseData();
    this.dataW3.y.cali = this.tassParams.w3_cali;
    this.dataW3.y.min = this.tassParams.w3_L;
    this.dataW3.y.max = this.tassParams.w3_H;
    this.dataLA = new AnalyseData();
    this.dataLA.y.cali = null;
    this.dataLA.y.max = this.tassParams.lA_T;
    this.CurveFiles.forEach(e => {
      if (e.viewState.checked && !e.viewState.filtered) {
        if ((e.viewState.result == TassViewResultType.OkInLimits || e.viewState.result == TassViewResultType.OkNotInLimits)
          && e.res.isDirty == false) // only use curves that have "current" results
        {
          const r = e.res.result;
          const isOk = e.viewState.result == TassViewResultType.OkInLimits;
          this.dataM1.y.data.push({ y: r.m1, measurementOk: isOk });
          this.dataM2.y.data.push({ y: r.m2, measurementOk: isOk });
          this.dataW3.y.data.push({ y: r.w3, measurementOk: isOk });
          if (r.g_AR && r.g_AR.idxCurveBeg >= 0 && r.g_AR.dM != 0) {
            var tmpT1 = (r.m2 - r.m1) / r.g_AR.dM;
            this.dataW3.y2.data.push({ y: tmpT1, measurementOk: isOk });
          } else {
            this.dataW3.y2.data.push({ y: 0, measurementOk: isOk });
          }
          this.dataLA.y.data.push({ y: e.res.result.la, measurementOk: isOk });
        }
        else {
          // add an "empty" point to indicate that the point is not used for calculation
          this.dataM1.y.data.push({ y: null, measurementOk: null });
          this.dataM2.y.data.push({ y: null, measurementOk: null });
          this.dataW3.y.data.push({ y: null, measurementOk: null });
          this.dataLA.y.data.push({ y: null, measurementOk: null });
        }
      }
    });
    this.calcstdev(this.dataM1, false);     // assume samples (no population)
    this.calcstdev(this.dataM2, false);
    this.calcstdev(this.dataW3, false);
    this.calcstdev(this.dataLA, false);
  }

  public fullscreenChart(idxChart: number): void {
    // make the chart fullscreen
    this.fullscreen[idxChart] = !this.fullscreen[idxChart];
    if (!this.fullscreen[idxChart]) {
      // reload the component - fix CSS layout issues with current chrome
      this.ForceComponentReload();
    }
    else {
      let lc = this.elRef.nativeElement.parentElement;
      this.RecalculateAndFixLayout(lc.offsetWidth, lc.offsetHeight);
    }
  }

  public zoomReset(chartIdx: number): void {
    switch(chartIdx) {
      case 0: this.refChartM1.zoomReset(); break;
      case 1: this.refChartM2.zoomReset(); break;
      case 2: this.refChartW3.zoomReset(); break;
      case 3: this.refChartLA.zoomReset(); break;
    }
  }

  public exportClicked(selItemCnt: number) {
    // show the export dialog/table modal
    this._modalWindowService.openDialogWithComponent(ModalExportComponent, {width: '90%', height: '700px'}, this._matDialog);
  }

  public selChanged(tassViewData: TassViewData) {
    console.log('AnalyseComp::selChanged', tassViewData);
    // TODO: highlight selected point in chart!
    this.selectIdx = null;
    let idx = 0;
    this.CurveFiles.every(e => {  // find index
      if (e.viewState.checked) {
        if (e.viewState.result == TassViewResultType.OkInLimits || e.viewState.result == TassViewResultType.OkNotInLimits) {
          if (e == tassViewData) {
            this.selectIdx = idx;
            return false;
          }
        }
        else { // "invisible" points cannot be selected
        }
        idx++;
      }
      return true;
    });
  }
  public checkChanged(tassViewData: TassViewData) {
    console.log('AnalyseComp::checkChanged', tassViewData);
    if (tassViewData == null) {
      // check all / uncheck all --> redraw
      this.recalculate();
    }
  }
}
