import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class BciMessagesFilterService {

  private _types = [];

  constructor() {}

  get filter(): Array<string> {
    return this._types;
  }

  set filter(filter: Array<string>) {
    this._types = filter;
  }

  public deactivateFilter(type: string): void {
    const index = this._types.indexOf(type);
    if (index > -1) {
      this.filter.splice(index, 1);
    }
  }
}
