import { Pipe, PipeTransform } from '@angular/core'

/*  Rounds to significant digits
    Default value of significant digits is 0.
    Usage:
        {{ value | significantNum:digits }}
*/

@Pipe({name: 'significantNum'})


export class SignificantNumberPipe implements PipeTransform {
    private locale: string
    constructor() {

        let defaultFmt = new Intl.NumberFormat();
        this.locale = defaultFmt.resolvedOptions().locale;

    }
    transform(value: number, digits: number = 0): string {
        if (value == null) return '';
        let s = '';
        if (digits == 0) {
          s = (+value).toFixed(0);
          return s;
        }
        // const localeOpt = {useGrouping: false, maximumSignificantDigits: digits};
        // const localeFmt = new Intl.NumberFormat(this.locale, localeOpt);
        // let text = ""
        // try {text = localeFmt.format(value)}
        // catch {
        //     text = "abc"
        // }

        // return localeFmt.format(value)
        s = (+value).toPrecision(digits);
        if (s.indexOf('e') >= 0) {
            // this can only happen if the number is too large, so only show the int part
            s = (+value).toFixed(0);
        }
        return s;
    }
}
