export class TassResult {
    resultCode: number;
    limitCheck: number;
    result: TTassResult;
    normalizedCurve: TTassCurve;
    isDirty?: boolean;         //
    constructor() {
      this.resultCode = -1;   // not yet initialized
      this.limitCheck = 0;
      this.result = new TTassResult();
      this.normalizedCurve = new TTassCurve();
      this.isDirty = null;    // not yet initialized
    }
}

export class TTassCurve {
    pointCount: number;
    a: number[];
    t: number[];
    constructor() {
      this.pointCount = 0;
      this.a = [];
      this.t = [];
    }
}

export class TTassLinReg {
    dM: number;
    dB: number;
    dR: number;
    idxCurveBeg: number;
    iPointCount: number;
}

export class TTassResult {
    Result: number;

    m1: number;
    m2: number;
    la: number;
    w3: number;

    w_Start: number;
    w_End: number;
    w1: number;
    w2: number;
    w_AM_left: number;
    w_AM_middle: number;
    w_AM_right: number;

    g_AL: TTassLinReg;
    g_AM: TTassLinReg;
    g_AR: TTassLinReg;

    idx_W_Start: number;
    idx_W_End: number;
    idx_W_AM_left: number;
    idx_W_AM_middle: number;
    idx_W_AM_right: number;

    idxNormBeg: number;
    idxNormEnd: number;

    t1: number;

    constructor() {

    }
}
