export class Logon {
  Connected: boolean;
  Connecting: boolean;
  Time: string;
  Parameter: DownloadFile;
  LogonReq: LogonReq;
  LogonRes: LogonRes;
  constructor() {
    this.Connected = false;
    this.Connecting = false;
    this.Time = '';
    this.Parameter = new DownloadFile();
    this.LogonReq = new LogonReq();
    this.LogonRes = new LogonRes();
  }
}

export class DownloadFile {
  Path: string;
  Text: string;
  Done: number;
  constructor() {
    this.Path = '';
    this.Text = '';
    this.Done = 0;
  }
}

export enum BSIFChnCompCategory { // Component category
  None        = 0,            //   no or unknown component
  Controller  = 1,            //   SE controller
  CompactBox  = 2,            //   CS controller
  ECMotor     = 3,            //   EC Drive/motor
  Transducer  = 4,            //   torque measurement sensor
  ServoAmp    = 5,            //   servo amplifier (LTxxx)
  ErgoSpin    = 6,            //   Ergospin tool
  Module      = 7,            //   Plugin card/module
  Mechanical  = 8,            //   mechanical component
};
export enum BSIFChnCompLogType {  // Logical component type
  None        = 0,            //   no or unknown component
  Controller  = 1,            //   SE/SEH controller
  CompactBox  = 2,            //   CS controller
  ECMotor     = 3,            //   EC Drive/motor
  Transducer  = 4,            //   torque measurement sensor
  ServoAmp    = 5,            //   servo amplifier (LTxxx)
  ErgoSpin    = 6,            //   Ergospin tool
  Module      = 7,            //   Plugin card/module
  MAdapter    = 8,            //   A - adapter
  MBearing    = 9,            //   G - spindle bearing
  MAngleHead  = 10,           //   W - angle head
  MPlanetary  = 11,           //   GE - planetary gearbox
  MFeedDrive  = 12,           //   S - Feed output drive
  MTransverse = 13,           //   ULG - transverse gearbox
  MOffsDrive  = 14,           //   VA - offset output drive (Vnx, VUx)
  MCrowFoot   = 15,           //   FL - crow foot
  MSpecial    = 16,           //   SO - special component
};
export enum BSIFChnCompMechType { // Mechanical component types (only if comp category is mechanical!)
  None        = 0,            //   no mechanical component (or unknown)
  Adapter     = 1,            //   A - adapter
  Bearing     = 2,            //   G - spindle bearing
  AngleHead   = 3,            //   W - angle head
  Planetary   = 4,            //   GE - planetary gearbox
  FeedDrive   = 5,            //   S - Feed output drive
  Transverse  = 6,            //   ULG - transverse gearbox
  OffsDrive   = 7,            //   VA - offset output drive (Vnx, VUx)
  CrowFoot    = 8,            //   FL - crow foot
  Special     = 9,            //   SO - special component
};

export class ToolComponent {
  state: number;
  name: string;
  ttnr: string;
  hwRev: string;
  swVer: string;
  serNo: string;
  usage: number;
  compPos: number;
  compLogID: number;
  compCategory: number;
  compMechType: number;
  compCustom: number;
  compRawID: number;
  cycles: number;
  rPM: number;                  //    0 = unknown/unused
  torqueSpec?: number;          //    R4/empty: maximum or rated torque value
  transmission?: number;        //    R4/empty: Transmission ratio (always 1.0 for ECMotor (nv. for ErgoSpin/generated by Wandler*Moment)
  efficiency?: number;          //    R4/empty: Efficiency value (always 1.0 for ECMotor (nv. for ErgoSpin/generated by Wandler*Moment)
  mfuFactor?: number;           //    R4/empty: Capability calibration value (only Transducer/ErgoSpin-Output Feed/Drive)
  redFactor?: number;           //    R4/empty: Redundancy calibration factor (only ECMotor/ErgoSpin-Output Feed/Drive)
  antriebSerNo: number;         //    Antrieb-Seriennummer
  lastCyCnt: number;            // ES42	Lastzykluszhler
  lastCycleLastCheck: number;   // ES43	Lastzyklus letzte Wartung
  lastCycleNextCheck: number;   // ES44	Lastzyklus nchste Wartung
  cyclePlannedCheck: number;    // ES47/MWG27   Schraubzyklus Info Wartung planen
  mFUProtocolID: string;        // char[50 + 1] ES45/MWG25	MFU Protokoll Dokument ID, String 50
  dateofPlannedCheck?: string;  // ES46/MWG26   VT/empty: Datum Info Wartung planen

  constructor() {
    this.name = '';
    this.ttnr = '';
    this.hwRev = '';
    this.swVer = '';
    this.serNo = '';
    this.torqueSpec = null;
    this.transmission = null;
    this.efficiency = null;
    this.mfuFactor = null;
    this.redFactor = null;
    this.mFUProtocolID = '';
    this.dateofPlannedCheck = null;
  }
}

export class ToolFile {
  name: string;
  type: number;
  size: number;
  date: number;
  constructor() {
    this.name = '';
    this.type = 0;
    this.size = 0;
    this.date = 0;
  }
}

export class LogonRes {
  components: ToolComponent[];
  files: ToolFile[];
  firmwareVersion: string;
  constructor() {
    this.components = [];
    this.files = [];
    this.firmwareVersion = '';
  }
}

