  // format a number to the given digis (precision)
  // Compared to the toPrecision() function, this will *not* do exponentials...
  valueFmt(x: number, digits: number = 0): string {
    if (x == null) return '';
    if (digits == 0) return (+x).toFixed(0);
    var s = (+x).toPrecision(digits);
    if (s.indexOf('e') >= 0) {
        // this can only happen if the number is too large, so only show the int part
        s = (+x).toFixed(0);
    }
    return s;
  }
//-----------------------------------------------------------------------------------------------
//Global
  saveAs() {
    if (!this.electronService.isElectronApp) {
      // Not running under Electron, ask if saving project or parameter file
      const ref = this.alertService.openAlertBox({
        leftButtonTitle: this.translate.instant(_('Project')),
        rightButtonTitle: this.translate.instant(_('Parameter')),
        outerLeftButtonTitle: this.translate.instant(_('Abort')),
        type: AlertType.None,
        title: this.translate.instant(_('Select download file type')),
      });
      ref.afterClosed().subscribe(res => {
        if (res === 'left') {
          this.saveProject();
        } else if (res == 'right') {
          this.saveParamsLocal();
        }
      });
    }
    else {
      // Show native dialog
      // ACHTUNG: Remote gibt es nicht mehr: https://github.com/electron/electron/issues/21408
      // var dialog = this.electronService.remote.dialog;
      // Instead, see: https://elanderson.net/2018/06/electron-net-save-dialog-file-writing/
      this.electronService.ipcRenderer.once("save-dialog-reply", (sender, path) => {
        if (!path) return;
        var txt = '';
        if (path.indexOf('.json') > -1) {
          //txt = JSON.stringify([CurveFiles,this.selection.selected[this.selection.selected.length - 1], this.LocalParams, this.EditParams, this.tassResult])
          txt = this.projectGetTxt();
        }
        else {
          txt = this.parametersService.toText(this.LocalParams);
        }
        // NOTE: this works only inside Electron (node.js):
        //       and requires nodeIntegration = true.
        // For in depth info and (correct) usage (with newer Electron versions), see:
        //  https://stackoverflow.com/questions/37994441/how-to-use-fs-module-inside-electron-atom-webpack-application
        const fs = (<any>window).require("fs");
        fs.writeFile(path, txt, function (err) {
          this.console.log(err);
        });
        this.electronService.shell.beep();
      });
      this.electronService.ipcRenderer.send("save-dialog", this.EditParams.sn);
    }
  }

  private projectGetTxt(): string {
    // create a temporary object for storing as text:
    let fileData = {
      fileVersion: 1,
      localParams: this.LocalParams,
      editParams: this.EditParams,
      items: CurveFiles
    }
    //txt = JSON.stringify([CurveFiles,this.selection.selected[this.selection.selected.length - 1], this.LocalParams, this.EditParams, this.tassResult])
    return JSON.stringify(fileData);
  }
  private projectClearAll() {
    this.tassViewDataService.clearAll(false);
    this.EditParams = new TassParameters();
    this.LocalParams = new TassParameters();
    //this.limR2_LR = 0;
    //this.limR2_M = 0;
  }
  private projectRestore(txt: string) : void {
    this.projectClearAll();
    try {
      var JSONdata  = JSON.parse(txt);
      // check version first
      if (JSONdata.fileVersion) {
          // we have a new version project file
          this.EditParams_old = JSONdata.editParams;
          this.EditParams = JSONdata.editParams;
          this.LocalParams = JSONdata.localParams;
          this.parametersService.publish(this.LocalParams);
          this.EditParams = JSONdata.editParams;
          //this.limR2_LR = this.EditParams.aL_AR_MinR * 100.0;
          //this.limR2_M = this.EditParams.aM_MinR * 100.0;
          let items = JSONdata.items;
          // with the new file format, we keep the results and parameters for each curve,
          // therefore we don't need to (slowly) recalculate anymore...
          this.tassViewDataService.loadAll(items);
//          items.forEach(element => {
//            this.tassViewDataService.calculateItem(element, TassViewEventType.ItemAdd);
//          });
      }
      else {
        // this is an old V1.0 version project
        this.EditParams_old = JSONdata[3];
        this.EditParams = JSONdata[3];
        //this.limR2_LR = this.EditParams.aL_AR_MinR * 100.0;
        //this.limR2_M = this.EditParams.aM_MinR * 100.0;
        //this.tassResult = JSONdata[4];
        this.parametersService.publish(JSONdata[2]);
        this.EditParams = JSONdata[3];
/*      this.tassResultService.publishNewResult(JSONdata[4], null)
        JSONdata[0].forEach(element => {
          this.curveFileService.create(element)
          //console.log(element)
          // @TODO: HACK: should have a pair of functions in the
          //       TassViewDataService to save/restore the project!!
          this.tassViewDataService.addCurveFile(element, this.EditParams);
        });*/
        // For performance reasons, only copy everything into an array, then
        // start a full recalculation...
        let tvd: TassViewData[] = [];
        JSONdata[0].forEach(element => {
          let v: TassViewData = new TassViewData();
          v.crv = element;
          v.par = this.EditParams;
          v.viewState.checked = true;
          v.viewState.filtered = false;
          tvd.push(v);
        });
        this.tassViewDataService.loadAll(tvd, false); // do NOT notify the GUI yet!
        CurveFiles = this.tassViewDataService.getDatastore().items;
        // trigger the recalculation...
        this.t1Recalculate();
      }
      // if (CurveFiles.length > 0) {
      //   this.drawChart(CurveFiles[CurveFiles.length - 1].crv, this.tassResult, this.EditParams);
      // }
    }
    catch {
      // show an error message!
      this.notify.error(_('Error loading project file - invalid file format!'));
      this.tassViewDataService.clearAll(true);
    }
  }

  saveProject() {
    var fn = 'Project_TASS';
    if (this.EditParams.sn != null && this.EditParams.sn.length > 0) {
      fn += '_' + this.EditParams.sn;
    }
    this.DownloadByHtmlTag(fn + '.json', 'text/json', this.projectGetTxt());
  }

    saveParamsLocal() {
      var fn = 'Parameter_TASS';
      if (this.EditParams.sn != null && this.EditParams.sn.length > 0) {
        fn += '_' + this.EditParams.sn;
      }
      this.DownloadByHtmlTag(fn + '.txt', 'text/plain', this.parametersService.toText(this.LocalParams));
    }

    private DownloadByHtmlTag(fileName: string, fileType: string, text: string) {
      if (!this.elemDownload) {
        this.elemDownload = document.createElement('a');
      }
      const dataUrl = 'data:' + fileType + ';charset=utf-8,' + encodeURIComponent(text);
      this.elemDownload.setAttribute('href', dataUrl);
      this.elemDownload.setAttribute('download', fileName);
      // simulate a mouse click event
      var event = new MouseEvent("click");
      this.elemDownload.dispatchEvent(event);
    }


