    ///! recalculate T1 for all checked curves
    t1Recalculate()
    {
      this.t1Index = -1;
      if (this.t1Next()) {
        this.TassResultText = '(scanning)';
        console.log("(scanning)");
        this.t1Loader = this.loaderService.showProgressBar();
      }
    }
    private t1Next(): boolean {
      this.t1Index++;
      for (; this.t1Index < CurveFiles.length; this.t1Index++) {
        if (this.recalcAll) {
          let cv = CurveFiles[this.t1Index];
          if (cv.res == null || cv.res?.isDirty != false) {
            // needs recalculation
            CurveFiles[this.t1Index].par = this.EditParams;
            this.tassViewDataService.calculateItem(CurveFiles[this.t1Index], TassViewEventType.ItemChange);
            return true;  // possibly more left...
          }
        } else {
          if (CurveFiles[this.t1Index].viewState.checked) {
            // update/overwrite the edit parames (so save and export will have these later)
            CurveFiles[this.t1Index].par = this.EditParams;
            // start calculation
            this.tassViewDataService.calculateItem(CurveFiles[this.t1Index], TassViewEventType.ItemChange);
            return true;  // possibly more left...
          }
        }
      }
      this.t1Index = -1;
      this.recalcAll = false;
      return false; // no more curves left
    }
    t1Copy() {
      this.EditParams.t1 = this.T1_avg;
      // trigger a recalculation, but do not clear out any T1 measurements...
      this._changeNotifier.next(0);
    }
    private updateT1Avg()
    {
      // calculate updated T1_avg
      this.T1_avg = null;
      var t1sum: number = 0;
      var t1count: number = 0;
      CurveFiles.forEach(element => {
        //@ts-ignore
        if (element.viewState.checked == true && !element.viewState.filtered && element.res?.result?.t1) {
          t1sum += element.res.result.t1; // +element.crv.state;
          t1count += 1;
        }
      });
      if (t1count > 0) {
        //@ts-ignore
        this.T1_avg = this.valueFmt(t1sum / t1count, 3);
      }
    }



