    // This function is called with every input element on the page changed.
    // WARNING:
    // - at the time this fires, the angular model might not be updated, i.e.
    //   reading a data value might still show the "old" value before the update
    // - debouncing is needed to fix this and to not overflow calculations..
    EditParamsChanged() {
      console.log("InbetriebnahmeComp::parameters changed - invalidating all curves...:", this.EditParams);
      this._changeNotifier.next(0);   // this will call EditParameterChangedDebounced() in a short time...
    }
    OnBlur(valRef, $event) {
      // check plausibility - do not allow any min > max or cali > max or cali < min
      let isOk: boolean = null;
      //const oldVal = this.EditParams[valRef];
      let oldVal = Number($event.target.oldVal);
      let newVal = Number($event.target.value);
      isOk = this.checkInputValue(isOk, valRef, "m1", newVal);
      isOk = this.checkInputValue(isOk, valRef, "m2", newVal);
      isOk = this.checkInputValue(isOk, valRef, "w3", newVal);
      if (isOk == null) {
        // no match, so there is no plausibility check attached, just trigger change detection
        this.EditParams[valRef] = newVal;
        console.log(`InbetriebnahmeComp::parameter ${valRef} changed to '${newVal}'`);
        this.EditParamsChangedDebounced();
    }
      else {
        // Check for plausibility and revert eventually
        // NOTE: this requires to also add the OnFocus event to the <input> element!
        if (isOk == true) {
          this.EditParams[valRef] = newVal;
          console.log(`InbetriebnahmeComp::parameter ${valRef} changed to '${$event.target.value}'`);
          //this._changeNotifier.next(0);   // this will call EditParameterChangedDebounced() in a short time...
          this.EditParamsChangedDebounced();
        }
        else {
          //this.EditParams[valRef] = oldVal; // oldVal + (Math.random()-0.5)/10000.0;
          $event.target.value = $event.target.oldVal;
          this.electronService?.shell?.beep();
        }
      }
    }
    OnFocus(valRef, $event) {
      let val = Number($event.target.value);
      $event.target.oldVal = val;
    }
    OnBlurPercent(valRef, $event) {
      this.EditParams[valRef] = Number($event.target.value) / 100.0;
      console.log(`InbetriebnahmeComp::parameter ${valRef} changed to '${$event.target.value}'`);
      //this._changeNotifier.next(0);   // this will call EditParameterChangedDebounced() in a short time...
      this.EditParamsChangedDebounced();
    }
    private checkInputValue(isOk: boolean, valRef: string, valTyp: string, newVal: number): boolean {
      if (isOk != null) {
        return isOk;  // already assigned elsewhere
      }
      if (!valRef.startsWith(valTyp)) {
        // not our tag/value type
        return null;
      }
      // we've found our value type, check limits
      switch(valRef) {
      case (valTyp + "_L"):   // require min < max
        isOk = (newVal < this.EditParams[valTyp + "_H"]);
        break;
      case (valTyp + "_cali"):
        isOk = (newVal < this.EditParams[valTyp + "_H"]) && (newVal > this.EditParams[valTyp + "_L"]);
        break;
      case (valTyp + "_H"):
        isOk = (newVal > this.EditParams[valTyp + "_L"]);
        break;
      }
      return isOk;
    }
    // The debounced handler - actually update everything...
    private EditParamsChangedDebounced()
    {
      let changed = true;
      let initialcall = false;
      if (this.EditParams_old == null) {
        initialcall = true;
      }
      try {
        if (JSON.stringify(this.EditParams_old) == JSON.stringify(this.EditParams)) {
            changed = false;
        }
      } catch {}
      this.EditParams_old = Object.assign({}, this.EditParams);
      if (changed) {
        console.log("InbetriebnahmeComp::parameters changed - debounced...:", this.EditParams);
        if (!initialcall) {
          // delete T1 values and set result state to dirty...
          this.tassViewDataService.InvalidateParamsAll();
        }
        // also clean the LA_T% derived value
        if (this.EditParams && this.EditParams.m2_cali) {
          this.LA_T_percent = (this.EditParams.lA_T / this.EditParams.m2_cali) * 100.0;
        } else {
          this.LA_T_percent = null;
        }
        this.parametersService.setEditParams(this.EditParams);
        this.listSelectionChanged(null);
        this.updateT1Avg();
      } else {
        console.log("InbetriebnahmeComp::parameters not changed:", this.EditParams);
      }
    }
