    ontxtFileSelect(input: HTMLInputElement) {
        const fileToRead = input.files.item(0)
        const fileReader = new FileReader();
        if(this.getFileExtension(fileToRead.name)== "txt"){
            var txtParams = [];
            fileReader.onload = () => {
                const data = fileReader.result;
                var txtData = data.toString();
                this.LocalParams = this.parametersService.fromText(txtData, fileToRead.name);
                this.EditParams = {}
                this.EditParams =  Object.assign({}, this.LocalParams);
                //this.limR2_LR = this.EditParams.aL_AR_MinR * 100.0;
                //this.limR2_M = this.EditParams.aM_MinR * 100.0;
                //this.parametersService.create(Object.assign({},Object.assign(Object.assign({},this.LocalParams), this.EditParams)));
                this.parametersService.publish(this.LocalParams);
                this.EditParamsChanged();
            };

        } else if (this.getFileExtension(fileToRead.name)== "json") {
            var txtParams = [];
            fileReader.onload = () => {
                const data = fileReader.result;
                var txtData = data.toString();
                this.projectRestore(txtData);
            }

        } else {
            this.openNotification("Error",this.translate.instant(_("Notification.Error.Only_txt_files_allowed")))
        }
        fileReader.readAsText(fileToRead);
    }

