    private updateCheckedItemCount(): void {
      // update our local variable to reflect the number of checked items
      // TODO: refactor: add getCheckedItemCount() to TassViewDataService.
      this.checkedItemCount = CurveFiles.reduce((acc, val) => {
        if (val.viewState.checked && !val.viewState.filtered) {
          return acc + 1;
        }
        return acc;
      }, 0);
    }
    listSelectionChanged(row: TassViewData): void {
      console.log('InbetriebnahmeComp::listSelectionChanged', row);
      if (row == null) {
        // Edit params changed - so only refresh, do not change the actual selection
        let visibleItemCount = 0;
        //visibleItemCount = this.kurvenListe?.visibleItemCount; // Hmm, the ref seems to not work correctly...?
        visibleItemCount = CurveFiles.filter((v) => {
          return !v.viewState.filtered
        }).length;
        if (CurveFiles.length == 0 || visibleItemCount == 0) {
          // list cleared!
          this.tassResult = null;
          this.refreshGuiResult(null);
          this.clearChart();
          return;
        }
        // Corner case - what happens, if we delete the currently selected element?
        // Fix by selecting the first element in the list
        if (this.selection?.selected?.length > 0) {
          let tvd = this.selection.selected[this.selection.selected.length - 1];
          // check, if this is still in the model, as the selection might keep a copy
          let found: TassViewData[] = CurveFiles.filter(e => e.crv.name == tvd.crv.name);
          if (found.length == 0) {
            // choose the first element
            row = CurveFiles[0];
            this.selection.select(row);
            this.updateT1Avg();
          }
        }
      }
      else {
        // change the selection to the given row, update the selection model
        if (this.selection) {
          this.selection.select(row);   //selection.toggle(row);
          this.updateT1Avg();
        }
        if (row.res && row.res.isDirty != null && row.res.isDirty == false) {
          // es hat sich nichts gendert, warum sollten wir das neu berechnen?
          this.tassResult = row.res;
          this.refreshGuiResult(row.res);
          this.refreshGuiChart(row.crv, row.res, row.par); //this.EditParams);
          return;
        }
      }

      // The user clicked the row --> send out a notification to recalculate the view
      this.refreshGuiResult(null);    // clear values
      this.TassResultText = this.translate.instant(_('(calculating)'));
      //this.tassResultService.publishNewResult();
      //this.tassService.processNewCurve();
      this.generateTassData(row);
    }

    private refreshGuiChart(tassCurve: CurveFile, tassResult: TassResult, tassParams: TassParameters): void {
      console.log('InbetriebnahmeComp::refreshGuiChart:', this.anyRowSelected);
      if (this.anyRowSelected) {
        const tvd: TassViewData = this.selection.selected[this.selection.selected.length - 1];
        const crv: CurveFile = tvd.crv;
        if (crv == tassCurve) {
          this.drawChart(crv, tassResult, tassParams);
        }
        else {
          console.log("... other chart ...");
        }
      }
    }
    private refreshGuiResult(tr: TassResult): void {
      if (tr == null) {
        // clear values
        this.M1 = undefined;
        this.M2 = undefined;
        this.W3 = undefined;
        this.LA = undefined;
        this.AL_min = undefined;
        this.AR_min = undefined;
        this.AL_R2 = undefined;
        this.AM_R2 = undefined;
        this.AR_R2 = undefined;
        this.TassResultText = '';
      } else {
        // update values
        this.M1 = tr.result.m1;
        this.M2 = tr.result.m2;
        this.W3 = tr.result.w3;
        this.LA = tr.result.la;
        this.AL_min = tr.result.g_AL?.iPointCount;
        this.AR_min = tr.result.g_AR?.iPointCount;
        this.AL_R2 = tr.result.g_AL?.dR;
        this.AM_R2 = tr.result.g_AM?.dR;
        this.AR_R2 = tr.result.g_AR?.dR;
        this.TassResultText = this.tassResultService.getLocalizedErrStr(tr.resultCode);
        if (tr.result.t1) {
          this.T1 = tr.result.t1;
        }
        else {
          this.T1 = 0;
        }
      }
    }

    // Request TASS analysis based on the currently selected curve
    private generateTassData(tvd: TassViewData = null)
    {
      // either use the last selected or the one passed in
      if (tvd == null) {
        // get the last one
        if (this.anyRowSelected) {
          tvd = this.selection.selected[this.selection.selected.length - 1];
          //const crv: CurveFile = this.selection.selected[this.selection.selected.length - 1];
          //var last_selected = crv.name;
        }
      }
      if (tvd == null || tvd.crv == null) {
        this.TassResultText = this.translate.instant(_('no data available'));
        return;
      }
      // Assign/overwrite the current parameters
      tvd.par = this.EditParams;
      // recalculate TASS data
      var params = {
        curve: {
          a: tvd.crv.angle,
          t: tvd.crv.torque,
          pointCount: tvd.crv.angle.length
        },
        parameters: tvd.par
      }
      //HE: OLD: this.tassService.requestCalculation(params, tvd.crv);
      this.tassViewDataService.calculateItem(tvd);
    }

