    // Chart can only be drawn (correctly), if the original curve data and the TASS results
    // are available
    drawChart(crv: CurveFile, tr: TassResult, params: TassParameters)
    {
      if (!this.tassChartComponent) {
        // When switching components, Angular seems to sometimes fire callback before the view is correctly
        // initialize.
        return;
      }
      // update curve view
      this.tassData.crv = crv;
      this.tassData.par = params;
      this.tassData.res = tr;
      this.tassData.normOnly = this.showNormOnly;
      this.tassChartComponent.redraw();
    }
    clearChart()
    {
      this.tassChartComponent?.cleanupChart();
    }

