    openLimitsModal(): void {
      this._modalWindowService.openDialogWithComponent(ModalLimitsComponent, {width: '700px', height: '350px'}, this._matDialog)
        .afterClosed()
        .subscribe(data => {
          if (data != null && data > 0 && data < 100) {
            const scale = (+data) / 100.0;
            if (this.EditParams.m1_cali) {
              const dM = this.EditParams.m1_cali * scale;
              this.EditParams.m1_H = this.EditParams.m1_cali + dM;
              this.EditParams.m1_L = this.EditParams.m1_cali - dM;
            }
            if (this.EditParams.m2_cali) {
              const dM = this.EditParams.m2_cali * scale;
              this.EditParams.m2_H = this.EditParams.m2_cali + dM;
              this.EditParams.m2_L = this.EditParams.m2_cali - dM;
            }
          }
        });
    }

    transform(value: number, digits: number = 0): string {
      if (value == null) return '';
      if (digits == 0) return (+value).toFixed(0);

      // return localeFmt.format(value)
      var s = (+value).toPrecision(digits);
      if (s.indexOf('e') >= 0) {
          // this can only happen if the number is too large, so only show the int part
          s = (+value).toFixed(0);
      }
      return s;
  }

