import { NgModule } from '@angular/core';
import { FossInfoComponent } from './foss-info/foss-info.component';
import { FossInfoDetailsComponent } from './foss-info-details/foss-info-details.component';
import { CloseDetailsGuard } from '@bci-web-core/core';
import { Routes, RouterModule } from '@angular/router';
import { FossInfoBaseModule } from './foss-info-base.module';

const routes: Routes = [
  {
    path: 'oss',
    component: FossInfoComponent,
    children: [
      {
        path: ':id',
        component: FossInfoDetailsComponent,
        canDeactivate: [CloseDetailsGuard]
      }
    ],
  },
];

@NgModule({
  imports: [
    FossInfoBaseModule,
    RouterModule.forChild(routes)
  ]
})
export class FossInfoModule { }

