
export class TassCalculateParams {
    curve: curve;
    parameters: parameters;
    constructor (){
        this.curve = new curve();
        this.parameters = new parameters();
    }
}

export class curve {
    pointCount: number;
    a: number[];
    t: number[];
}

export class parameters  {
    m1_cali?: number;
    m1_L?: number;
    m1_H?: number;

    m2_cali?: number;
    m2_L?: number;
    m2_H?: number;

    w3_cali?: number;
    w3_L?: number;
    w3_H?: number;

    lA_T?: number;

    sB_RNG?: number;
    sB_T?: number;
    ms?: number;
    me?: number;
    t1?: number;

    aL_Min?: number;
    aR_Min?: number;

    aM_MinR?: number;
    aL_AR_MinR?: number;
}

