import { Component, EventEmitter, Input, Output } from '@angular/core';
import { BciSharedIntl } from '../shared-intl';
import { BciProductInfoColumn } from './product-info-column.model';

@Component({
  selector: 'bci-product-info',
  templateUrl: './product-info.component.html',
  styleUrls: ['./product-info.component.scss']
})
export class ProductInfoComponent {
  /** Whether the product info is expanded. */
  @Input() set isExpanded(value: boolean) {
    this._isExpanded = value;
    this.isExpandedChange.emit(this._isExpanded);
  }

  get isExpanded(): boolean {
    return this._isExpanded;
  }

  @Output() isExpandedChange = new EventEmitter<boolean>();

  /** Title of the product info */
  @Input() title: string;

  /**
   * A maximum of three columns displaying the product information. If two or three columns are shown a title for
   * each column is mandatory.
   */
  get columns(): BciProductInfoColumn[] {
    return this._columns;
  }

  @Input() set columns(value: BciProductInfoColumn[]) {
    if (value && value.length > 3) {
      throw Error('A maximum of three columns can be displayed in the product info.');
    }
    if (value && value.length > 1 && value.some(elem => elem.title == null || elem.title.length === 0)) {
      throw Error('If multiple columns are shown each column must have a title.');
    }
    this._columns = value;
  }

  /** URL pointing to the a documentation. The documentation will open in a new tab. */
  @Input() documentationUrl: string;

  get hasDocumentation(): boolean {
    return this.documentationUrl != null && this.documentationUrl.length > 0;
  }

  private _columns: BciProductInfoColumn[];

  private _isExpanded = true;

  constructor(public intl: BciSharedIntl) {
  }
}
