import { Component, OnInit } from '@angular/core';
import { MatIconRegistry } from '@angular/material/icon';
import { DomSanitizer, Title } from '@angular/platform-browser';
import { ActivatedRoute, Router } from '@angular/router';
import {
  BciMessagesService,
  LoggerService,
  LogoutComponent,
  NavigationService,
  SidebarNavItem,
  LogLevel,
} from '@bci-web-core/core';
import { LangChangeEvent, TranslateService } from '@ngx-translate/core';
import { Observable } from 'rxjs';
import { map, mergeMap, tap } from 'rxjs/operators';
import { angularIcon } from './svg-demo';
import { marker as _ } from '@biesbjerg/ngx-translate-extract-marker';

@Component({
  selector: 'bci-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
})
export class AppComponent implements OnInit {
  title = 'TASS';
  titleLineTwo = 'Set-up';
  localesList = [
    { code: 'en-US', label: 'English' },
    { code: 'de', label: 'Deutsch' }
  ];
  sidebarLinks: SidebarNavItem[] = [];
  sidebarFooterLinks: SidebarNavItem[] = [];
  extendedNavigation = false;

  constructor(
    private titleService: Title,
    private bciMessagesService: BciMessagesService,
    private navigationService: NavigationService,
    private logger: LoggerService,
    private router: Router,
    private route: ActivatedRoute,
    iconRegistry: MatIconRegistry,
    sanitizer: DomSanitizer,
    private translate: TranslateService
  ) {
    iconRegistry.addSvgIconLiteral(
      'angularIcon', sanitizer.bypassSecurityTrustHtml(angularIcon));
    // initialize the translation language...
    var lang = localStorage.getItem('language');
    if (!lang) {
      lang = this.translate.getBrowserLang();
    }
    this.translate.use(lang);
  }

  getSidebarLinks(): Observable<SidebarNavItem[]> {
    return this.route.queryParamMap.pipe(
      map((params) => params.get('extendedNavigation')),
      tap((param) => {
        if (param === 'true') {
          this.extendedNavigation = true;
        } else if (param === 'false') {
          this.extendedNavigation = false;
        }
      }),
      mergeMap(() => this.navigationService.getNavigationItems()),
      map((items) => {
        if (this.extendedNavigation) {
          items.find((item) => item.title === 'Navigation').items.find((item) => item.title === 'Breadcrumb').items = [
            {
              title_i18n_key: "Navigation.GettingStarted",
              title: 'Getting Started',
              url: 'navigation/breadcrumbs-demo/getting-started',
              routerLinkActiveOptions: {
                exact: false,
              },
            },
            {
              title_i18n_key: "Navigation.Summary",
              title: 'Summary',
              url: 'navigation/breadcrumbs-demo/summary',
              routerLinkActiveOptions: {
                exact: false,
              },
            },
          ];
        }
        return items;
      })
    );
  }
  // Ugly hack to workaround missing i18n features in the Bosch core components
  getFooterLinks(): SidebarNavItem[] {
    return [
      this.bciMessagesService.sidebarLink(),
      {
        title_i18n_key: 'Navigation.Settings',
        title:  this.translate.instant(_('Navigation.Settings')),
        url: 'einstellungen',
        icon: 'bosch-ic-settings',
      },
      {
        title_i18n_key: 'Navigation.OSSInfo',
        title: this.translate.instant(_('Navigation.OSSInfo')),
        url: '/oss',
        icon: 'bosch-ic-info-i-frame',
      },
      this.navigationService.getImprintSidebarLink(),
    ];
  }

  ngOnInit() {
    this.logger.logLevel = LogLevel.TRACE;
    this.titleService.setTitle(this.title);
    this.getSidebarLinks().subscribe((sidebarLinks) => (this.sidebarLinks = sidebarLinks));
    this.translate.onLangChange.subscribe((event: LangChangeEvent) => {
      // translate again...
      this.getSidebarLinks().subscribe((sidebarLinks) => {
        this.sidebarLinks = sidebarLinks;
        // now translate everything...
      });
      this.sidebarFooterLinks = this.getFooterLinks();
    });
  }

  logout() {
    this.logger.info('Logout mock');
    this.router.navigate(['login']);
  }
}
