import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { CurveFile } from '../models/curvefile';
import { HttpClient } from '@angular/common/http'
import { worker } from 'cluster';
import { bs300if_curveResponse } from '../models/bs300if';

@Injectable({
    providedIn: 'root'
})
export class CurveFileService {

    private _curve = new BehaviorSubject<CurveFile[]>([]);
    readonly curves$ = this._curve.asObservable();

    private curves : CurveFile[] = [];

    constructor(private http: HttpClient) { }

    loadAll () {
        this.curves = [];
        this._curve.next(this.curves);
    }

    create (item: CurveFile) {
        this.curves.push(item);
        this._curve.next(Object.assign([], this.curves))
    }

    clearAll () {
        this.curves = [];
    }

    remove (name: string) {
        this.curves.forEach((t, i) => {
            if (t.name == name) {
              this.curves.splice(i, 1);
            }
            this._curve.next(Object.assign([], this.curves));
        });
    }

    getCurves () {
        return this.curves
    }

    getCurveByName(name: string): CurveFile {
      this.curves.forEach((t, i) => {
        if (t.name == name) {
          return t;
        }
      });
      return null;
    }

    toCsv(crv: CurveFile): string {
      var tmp: string = '';
      for (var i = 0; i < crv.angle.length; i++) {
        tmp += crv.angle[i];
        tmp += '; ';
        tmp += crv.torque[i];
        tmp += '\n';
      }
      tmp += '\n\n***************\n';
      return tmp;
    }

    fromCrvFile(fileName: string, crvData: bs300if_curveResponse): CurveFile {
      // Convert from the BS300IF api structure to the crvFile structure
      // Kurven umrechnen in steigende Winkel...
      var posAngles: number[] = [];
      var a0 = crvData.angleValues[0];
      posAngles.push(a0);
      for (var i = 1; i < crvData.pointCount; i++) {
        var da = crvData.angleValues[i] - crvData.angleValues[i-1];
        a0 += (da > 0) ? da : -da;
        posAngles.push(a0);
      }
      const crvFile = {
        checkbox: false,
        name: fileName, // fileElement.name,
        state: 'n.a.',
        program: crvData.program,    // unknown
        channel: crvData.channel,
        curveSeq: crvData.curveSeq,
        qC1: crvData.qC1,
        qC2: crvData.qC2,
        angle: posAngles,
        torque: crvData.torqueValues,
        time: crvData.time,
      }
      return crvFile;
    }

    fromCsvText(fileName: string, txtData: string): CurveFile {
      var angle = []
      var torque = []
      var lines = txtData.split('\n')
      lines.forEach(element => {
        //var tmp = element.replace(/\s+/g, '').replace('\n','').replace(/,/g, '.').split(';')
        var tmp = element.replace(/;/g, ' ').replace(/,/g, '.').match(/\s*(\S*)\s*(\S*)\s*/);
        if (tmp.length >= 3) {
          var t1: number = parseFloat(tmp[1]);
          var t2: number = parseFloat(tmp[2]);
          if (!Number.isNaN(t1) && !Number.isNaN(t2)) {
            angle.push(t1);
            torque.push(t2);
          }
        }
      });
      const crvFile = {
        checkbox: false,
        name: fileName, // fileElement.name,
        state: 'n.a.',
        program: -1,    // unknown
        channel: 0,
        curveSeq: 0,
        qC1: 0,
        qC2: 0,
        angle: angle,
        torque: torque,
        time: new Date(),
      }
      return crvFile;
    }

    async readFileAsTextAsync(file) {
      let txtData = await new Promise((resolve) => {
          let fileReader = new FileReader();
          fileReader.onload = (e) => resolve(fileReader.result);
          fileReader.readAsText(file);
      });
      return txtData;
    }
    async readFileAsTextAsync2(file) {
      let txtData = await new Promise((resolve) => {
          let fileReader = new FileReader();
          fileReader.onload = (e) => resolve(fileReader.result);
          fileReader.readAsText(file);
      });
      return { file: file, data: txtData };
    }
    async readFileAsBase64Async(file) {
      let binData = await new Promise((resolve) => {
          let fileReader = new FileReader();
          fileReader.onload = (e) => resolve(fileReader.result);
          fileReader.readAsBinaryString(file);
      });
      //@ts-ignore
      let base64Text = btoa(binData);
      return { file: file, data: base64Text };
    }

/*
    readFileAsTextPromise(file) {
      return new Promise((resolve) => {
          let fileReader = new FileReader();
          fileReader.onload = (e) => resolve({ file: file, data: fileReader.result });
          fileReader.readAsText(file);
      });
    }
*/
    // Pass in a HTML element suitable for a FielReader:readAsText()
    _xx_fromCsvBlob(fileElement: any): CurveFile {

      var angle = []
      var torque = []

      const fileReader = new FileReader();
      fileReader.onload = () => {
        const data = fileReader.result;
        var txtData = data.toString();
        var lines = txtData.split('\n')
        lines.forEach(element => {
            //var tmp = element.replace(/\s+/g, '').replace('\n','').replace(/,/g, '.').split(';')
            var tmp = element.replace(/;/g, ' ').replace(/,/g, '.').match(/\s*(\S*)\s*(\S*)\s*/);
            if (tmp.length >= 3) {
              var t1: number = parseFloat(tmp[1]);
              var t2: number = parseFloat(tmp[2]);
              if (!Number.isNaN(t1) && !Number.isNaN(t2)) {
                angle.push(t1);
                torque.push(t2);
              }
            }
          })
        };
        fileReader.readAsText(fileElement);
        const crvFile = {
          checkbox: false,
          name: fileElement.name,
          state: 'n.a.',
          program: -1,    // unknown
          channel: 0,
          curveSeq: 0,
          qC1: 0,
          qC2: 0,
          angle: angle,
          torque: torque,
          time: new Date(),
      }
      return crvFile;
    }
}
