import { Injectable } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';

@Injectable({
  providedIn: 'root'
})
export class AppsettingsService {

  private _currentLanguage: string;
  private _decimalSeperator: string;
  private _fieldSeperator: string;

  constructor(
    private translate: TranslateService
  )
  {
  }

  public get currentLanguage(): string {
    // ask the language service every time this is accessed, but keep a local cache, too
    if (this.translate.currentLang != undefined) {
      this._currentLanguage = this.translate.currentLang
    } else {
        this._currentLanguage = this.translate.getDefaultLang()
    }
    return this._currentLanguage;
  }
  public set currentLanguage(language: string) {
    // TODO: might check for validity
    this.translate.use(language)
    localStorage.setItem('language', language);
    this._currentLanguage = language;
  }

  public get decimalSeperator(): string {
    this._decimalSeperator = localStorage.getItem('decimalSeperator');
    if (!this._decimalSeperator??false) {
      this._decimalSeperator = this.isLikeDE() ? ',' : '.';
      localStorage.setItem('decimalSeperator', this._decimalSeperator);
    }
    return this._decimalSeperator;
  }
  public set decimalSeperator(seperator: string) {
    // TODO: might check for validity
    this._decimalSeperator = seperator;
    localStorage.setItem('decimalSeperator', this._decimalSeperator);
  }

  public get fieldSeperator(): string {
    this._fieldSeperator = localStorage.getItem('fieldSeperator');
    if (!this._fieldSeperator??false) {
      this._fieldSeperator = this.isLikeDE() ? ';' : ',';
      localStorage.setItem('fieldSeperator', this._fieldSeperator);
    }
    return this._fieldSeperator;
  }
  public set fieldSeperator(seperator: string) {
    // TODO: might check for validity
    this._fieldSeperator = seperator;
    localStorage.setItem('fieldSeperator', this._fieldSeperator);
  }

  private isLikeDE(): boolean {
    return this._currentLanguage == 'de' || this._currentLanguage == 'nl';
  }

}
