import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

/**
 * @ignore
 */
@Injectable({
  providedIn: 'root'
})
export class FossInfoService {
  constructor(private http: HttpClient) { }

  components: FossComponent[];

  getComponents(): Observable<FossComponent[]> {
    return this.http.get<FossComponent[]>('/assets/oss-info.json').pipe(
      map(data => this.components = data)
    );
  }
}

/**
 * @ignore
 */
export interface FossComponent {
  name: string;
  version?: string;
  homepage?: string;
  copyright?: Copyright;
  licenses?: License[];
}

/**
 * @ignore
 */
export interface Copyright {
  text: string;
  notice?: string;
}

/**
 * @ignore
 */
export interface License {
  name: string;
  spdx?: string;
  text?: string;
}
