export class AnalyseData_Item
{
  y: number;
  measurementOk: boolean;   // true, if this can be used for statistical calculations
}
export class AnalyseData_Y
{
  data: AnalyseData_Item[];
  avg: number;   // Mittelwert
  cali: number;  // Kalibrierwert
  min: number;
  max: number;
  dev: number;   // Standardabweichung
  cmt: number;
  cmkt: number;
  constructor() {
    this.data = [];
    this.avg = 0.0;
    this.cali = 0.0;
    this.min = 0.0;
    this.max = 0.0;
    this.dev = 0.0;
    this.cmt = 0.0;
    this.cmkt = 0.0;
  }
}
export class AnalyseData_Y2
{
  data: AnalyseData_Item[];
  constructor() {
    this.data = [];
  }
}

export class AnalyseData
{
  y: AnalyseData_Y;
  y2: AnalyseData_Y2;
  constructor() {
    this.y = new AnalyseData_Y();
    this.y2 = new AnalyseData_Y2();
  }
}

