import { Component, OnInit, Inject, OnDestroy } from '@angular/core';
import { BehaviorSubject, Observable, Subscription } from 'rxjs';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { CollectionViewer, DataSource } from "@angular/cdk/collections";
import { TassViewData, TassViewUpdateData, TassViewEventType, TassViewResultType } from "../../shared/models/tassviewdata";
import { TassParameters } from '../../shared/models/tassparameters';
import { TassViewDataService } from '../../shared/frontend/tassviewdataservice';
import { ParametersService } from '../../shared/frontend/parameter.service';
import { TranslateService } from '@ngx-translate/core';
import { ElectronService } from 'ngx-electron';
import { AppsettingsService } from '../../shared/appsettings.service';

@Component({
  selector: 'bci-modalexport',
  templateUrl: './modalexport.component.html',
  styleUrls: ['./modalexport.component.scss']
})
export class ModalExportComponent implements OnInit, OnDestroy {

  paramObservable$: Observable<TassParameters>;
  paramSubscription$: Subscription;
  tassViewDataSubscription$: Subscription;
  CurveFiles: TassViewData[] = [];
  dataSource: PaginatorDataSource<TassViewData>;
  displayedColumns = [
    'sn','name','prg',
    'state', // TASSinLimits
    'M1','M2','W3','T1','LA',
    'LAPer',
    'ALmin',
    'ARmin',
    'ALR2',
    'AMR2',
    'ARR2',
    //M1Cali,M1UL,M1LL,
    //M2Cali,M2UL,M2LL,
    //W3Cali,W3UL,W3LL,
    //LAUL,ALminLL,ARminLL,ALR2LL,AMR2LL,ARR2LL,
    //MS,ME,SBT,SBRNG
  ];
  decimalSeperator: string;
  fieldSeperator: string;

  constructor(
    private dialogRef: MatDialogRef<ModalExportComponent>,
    private parametersService: ParametersService,
    private translate: TranslateService,
    private tassViewDataService: TassViewDataService,
    private appSettings: AppsettingsService,
    private electronService: ElectronService,
    @Inject(MAT_DIALOG_DATA) public data: any

  ) { }

  ngOnInit(): void
  {
    this.paramObservable$ = this.parametersService.params$;
    this.paramSubscription$ = this.paramObservable$.subscribe((param: TassParameters) => {
      console.log('ModalExportComp::OnParametersChanged: ', param);
      //this.LocalParams = param;
    });
    this.tassViewDataSubscription$ = this.tassViewDataService.items$.subscribe((item: TassViewUpdateData) => {
      console.log('ModalExportComp::OnTassViewDataChange: ', item);
      // something has changed in our curve list - copy it for view here
      this.CurveFiles = item.items;
      this.loadDataSource();
    });
    this.decimalSeperator = localStorage.getItem('decimalSeperator');
    this.fieldSeperator = localStorage.getItem('fieldSeperator');
  }
  //-----------------------------------------------------------------------------------------------
  ngOnDestroy() {
    this.tassViewDataSubscription$.unsubscribe();
    this.paramSubscription$.unsubscribe();
  }

  cancel(): void {
    this.dialogRef.close();
  }
  close(): void {
    console.log('Closed by backdrop or icon');
    this.dialogRef.close();
  }
  save(): void {
    //this.LocalParams.m1_L = this.M1_cali - (this.M1_cali / 10);
    //this.parameterService.publish(this.LocalParams);
    if (this.electronService.isElectronApp) {
      // Show native dialog
      // ACHTUNG: Remote gibt es nicht mehr: https://github.com/electron/electron/issues/21408
      // var dialog = this.electronService.remote.dialog;
      // Instead, see: https://elanderson.net/2018/06/electron-net-save-dialog-file-writing/
      this.electronService.ipcRenderer.once("save-csv-reply", (sender, path) => {
        if (!path) return;
        let txt = this.getCSVtxt();
        // NOTE: this works only inside Electron (node.js):
        //       and requires nodeIntegration = true.
        // For in depth info and (correct) usage (with newer Electron versions), see:
        //  https://stackoverflow.com/questions/37994441/how-to-use-fs-module-inside-electron-atom-webpack-application
        const fs = (<any>window).require("fs");
        fs.writeFile(path, txt, {flag: 'w'}, (err) => {
          if (err) {
            console.log(err);
          } else {
            console.log("File written successfully");
          }
        });
        this.electronService.shell.beep();
      });
      this.electronService.ipcRenderer.send("save-csv", '');
    }
    else {
      // debug only.
      let txt = this.getCSVtxt();
    }
    this.dialogRef.close();
  }

  // Generate the CSV file data (as text string)
  // use the "current" locale to format the CSV text
  private getCSVtxt(locale: string = null): string {
    const columns: string[] = [ 'TASSSNR','CurveName','Prg','TASSinLimits','M1','M2','W3','T1','LA','LAPer','ALmin','ARmin','ALR2','AMR2','ARR2','M1Cali','M1UL','M1LL','M2Cali','M2UL','M2LL','W3Cali','W3UL','W3LL','LAUL','ALminLL','ARminLL','ALR2LL','AMR2LL','ARR2LL','MS','ME','SBT','SBRNG' ];
    // NOTE: now always use the "en-us" locale, but set formatting options, so
    // the converted number will always have a dot decimal seperator, then string
    // replace it... (isn't there a useful method for this in JavaScript???)
    const localeOpt = {useGrouping: false, maximumFractionDigits: 4};
    const localeFmt = new Intl.NumberFormat('en-us', localeOpt);
    //txt = JSON.stringify([CurveFiles,this.selection.selected[this.selection.selected.length - 1], this.LocalParams, this.EditParams, this.tassResult])
    //@ts-ignore
    // let txt: string = columns.toLocaleString(locale) + '\n';
    const fldSep = this.appSettings.fieldSeperator;
    const decSep = this.appSettings.decimalSeperator;
    let txt: string = columns.join(fldSep) + '\n';
    this.CurveFiles.forEach(v => {
        if (v.viewState.checked) {
          // add a csv row
          const vals = this.getCSVvals(v, localeFmt, decSep);
//!-- ts-ignore
          txt += vals.join(fldSep) + '\n';
        }
      });
    return txt;
  }
  private getCSVvals(row: TassViewData, localeFmt: Intl.NumberFormat, decSep: string): string[] {
    //TASSSNR;CurveName;Prg;TASSinLimits;M1;M2;W3;T1;LA;LAPer;ALmin;ARmin;ALR2;AMR2;ARR2;M1Cali;M1UL;M1LL;M2Cali;M2UL;M2LL;W3Cali;W3UL;W3LL;LAUL;ALminLL;ARminLL;ALR2LL;AMR2LL;ARR2LL;MS;ME;SBT;SBRNG
    let txt: string[] = [];
    txt.push(row.par?.sn);
    txt.push(row.crv?.name);
    txt.push(row.crv?.program?.toString());
    txt.push(row.res?.resultCode == 0 && row.res?.limitCheck == 0 ? 'TRUE' : 'FALSE');
    //txt.push(row.res?.result?.m1.toLocaleString(null, localeFmt));
    txt.push(this.fmt(localeFmt, decSep, row.res?.result?.m1));
    txt.push(this.fmt(localeFmt, decSep, row.res?.result?.m2));
    txt.push(this.fmt(localeFmt, decSep, row.res?.result?.w3));
    txt.push(this.fmt(localeFmt, decSep, row.res?.result?.t1));
    txt.push(this.fmt(localeFmt, decSep, row.res?.result?.la));
    if (row.par?.m2_cali != 0) {
      txt.push(this.fmt(localeFmt, decSep, 100.0 * row.res?.result?.la / row.par.m2_cali));
    } else {
      txt.push('');
    }
    txt.push(row.res?.result?.g_AL?.iPointCount.toString());
    txt.push(row.res?.result?.g_AR?.iPointCount.toString());
    txt.push(this.fmt(localeFmt, decSep, row.res?.result?.g_AL?.dR));
    txt.push(this.fmt(localeFmt, decSep, row.res?.result?.g_AM?.dR));
    txt.push(this.fmt(localeFmt, decSep, row.res?.result?.g_AR?.dR));
    txt.push(this.fmt(localeFmt, decSep, row.par?.m1_cali));
    txt.push(this.fmt(localeFmt, decSep, row.par?.m1_H));
    txt.push(this.fmt(localeFmt, decSep, row.par?.m1_L));
    txt.push(this.fmt(localeFmt, decSep, row.par?.m2_cali));
    txt.push(this.fmt(localeFmt, decSep, row.par?.m2_H));
    txt.push(this.fmt(localeFmt, decSep, row.par?.m2_L));
    txt.push(this.fmt(localeFmt, decSep, row.par?.w3_cali));
    txt.push(this.fmt(localeFmt, decSep, row.par?.w3_H));
    txt.push(this.fmt(localeFmt, decSep, row.par?.w3_L));
    txt.push(row.par?.aL_Min.toString());
    txt.push(row.par?.aR_Min.toString());
    txt.push(this.fmt(localeFmt, decSep, row.par?.aL_AR_MinR));
    txt.push(this.fmt(localeFmt, decSep, row.par?.aM_MinR));
    txt.push(this.fmt(localeFmt, decSep, row.par?.aL_AR_MinR));
    txt.push(this.fmt(localeFmt, decSep, row.par?.ms));
    txt.push(this.fmt(localeFmt, decSep, row.par?.me));
    txt.push(this.fmt(localeFmt, decSep, row.par?.sB_T));
    txt.push(this.fmt(localeFmt, decSep, row.par?.sB_RNG));
    return txt;
  }
  private fmt(localeFmt: Intl.NumberFormat, decSep: string, v: number): string {
    return v ? localeFmt.format(v).replace('.', decSep) : '';
  }

  private loadDataSource() {
    // create a new datasource, but only use the selected items
    let data: TassViewData[] = [];
    this.CurveFiles.forEach(v => {
      if (v.viewState.checked) {
        data.push(v);
      }
    });
    this.dataSource = new PaginatorDataSource<TassViewData>(data);
  }

  isDirty(v: TassViewData): boolean
  {
    return v?.res?.isDirty ?? true;
  }
}

//-----------------------------------------------------------------------------------------------

export class PaginatorDataSource<T> implements DataSource<T> {

  _allData: T[] = [];
  _paginated: T[] = [];
  pageData: {
      pageSize: number;
      pageIndex: number;
  } = {
      pageSize: 100,
      pageIndex: 0
  };
  private readonly _renderData = new BehaviorSubject<T[]>([]);

  constructor(data?: T[]) {
      this.data = data || [];
  }

  get pageSize(): number {
      return this.pageData.pageSize;
  }

  set pageSize(pageSize: number) {
      this.pageData.pageSize = pageSize;
      this._filter();
  }

  get pageIndex(): number {
      return this.pageData.pageIndex;
  }

  set pageIndex(index: number) {
      this.pageData.pageIndex = index;
      this._filter();
  }

  get numberOfItems(): number {
      return this._allData.length;
  }

  get data(): T[] {
      return [ ...this._allData ];
  }

  set data(data: T[]) {
      this._allData = data;
      this._filter();
  }

  get paginatedData(): T[] {
      return [ ...this._paginated ];
  }

  connect(collectionViewer: CollectionViewer): Observable<T[] | ReadonlyArray<T>> {
      return this._renderData;
  }

  _filter() {
      const startFromPage = this.pageData.pageIndex === 0 ? 0 : this.pageData.pageSize * this.pageData.pageIndex;
      const pageDataSize = startFromPage + this.pageData.pageSize;
      this._paginated = this._allData.slice(startFromPage, pageDataSize);
      this._renderData.next(this._paginated);
  }

  disconnect(collectionViewer: CollectionViewer): void {}
}
