  //       takeUntil(this.ngUnsubscribe)
  //     ).subscribe(elem => {
  //       this.sourceFilterSubject.next(elem);
  //     });
  //   }
  // }

  /**
   * Applies a target filter.
   * @param filter
   */
  setTargetFilter(filter: false | string | BciPicklistFilter | Observable<BciPicklistFilter>): void {
    if (this.targetFilter instanceof Observable) {
      this.targetFilterUnsubscribe.next();
    }

    if (typeof filter === 'boolean') {
      this.targetFilter = false;
      this.updateProcessedSources('target');
    } else if (typeof filter === 'string') {
      this.targetFilterSubject.next(filter);
    } else if (filter instanceof Observable) {
      filter.pipe(takeUntil(this.targetFilterUnsubscribe), takeUntil(this.ngUnsubscribe)).subscribe((elem) => {
        this.targetFilterSubject.next(elem);
      });
    } else {
      this.targetFilterSubject.next(filter);
    }
  }
