import { Injectable } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { ModalWindowService } from '../modal-window/modal-window.service';
import { AlertDialogComponent } from './alert-dialog/alert-dialog.component';
import { AlertBoxOptions } from './models/alert-box-options';

@Injectable({
  providedIn: 'root'
})
export class AlertService {

  constructor(private dialog: MatDialog,
              private modalWindowService: ModalWindowService) {
  }

  openAlertBox(options: AlertBoxOptions): MatDialogRef<AlertDialogComponent> {
    return this.modalWindowService.openDialogWithComponent<AlertDialogComponent, AlertBoxOptions>(AlertDialogComponent, {
      disableClose: true,
      data: options,
      autoFocus: false,
      panelClass: 'bci-web-core-alert-pane'
    });
  }
}
