import { AfterViewInit, Component, ElementRef, Inject, ViewChild } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { AlertBoxOptions } from '../models/alert-box-options';
import { AlertClosingType, AlertType, PrimaryButton } from '../models/alert.enums';

@Component({
  selector: 'bci-alert',
  templateUrl: './alert-dialog.component.html',
  styleUrls: ['./alert-dialog.component.scss'],
})
export class AlertDialogComponent implements AfterViewInit {
  @ViewChild('outerLeftBtnRaised', { read: ElementRef }) outerLeftBtnRaised: ElementRef;
  @ViewChild('leftBtnRaised', { read: ElementRef }) leftBtnRaised: ElementRef;
  @ViewChild('rightBtnRaised', { read: ElementRef }) rightBtnRaised: ElementRef;

  AlertType = AlertType;
  AlertClosingType = AlertClosingType;
  PrimaryButton = PrimaryButton;

  constructor(
    @Inject(MAT_DIALOG_DATA) public data: AlertBoxOptions,
    public dialogRef: MatDialogRef<AlertDialogComponent>
  ) {}

  close(type: AlertClosingType): void {
    this.dialogRef.close(type);
  }

  ngAfterViewInit(): void {
    switch (this.data.primaryButton) {
      case PrimaryButton.LeftButton:
        this.leftBtnRaised.nativeElement.focus();
        break;
      case PrimaryButton.RightButton:
        this.rightBtnRaised.nativeElement.focus();
        break;
      case PrimaryButton.OuterLeftButton:
        this.outerLeftBtnRaised.nativeElement.focus();
        break;
    }
  }
}
