import { Injectable } from '@angular/core';
import { LangChangeEvent, TranslateService } from '@ngx-translate/core';
import { marker as _ } from '@biesbjerg/ngx-translate-extract-marker';

@Injectable()
export class BciMessagesIntl {

  /* A title for messages dialog */
  title = 'Messages';

  /* A placeholder for type filter */
  typeFilter = 'Filter Type';

  /* Message type */
  information = 'Information';

  /* Message type */
  success = 'Success';

  /* Message type */
  warning = 'Warning';

  /* Message type */
  error = 'Error';

  /* A label for delete button */
  delete = 'Delete';

  /* A tooltip for deleting selected messages */
  deleteSelected = 'Delete selected messages';

  /* A tooltip for deleting all messages */
  deleteAll = 'Delete all messages';

  /* A column label for date */
  date = 'Date';

  /* A column label for type */
  type = 'Type';

  /* A column label for message */
  message = 'Message';

  /* A column label for go to link */
  goTo = 'Go to';

  /* A placeholder if no messages existing */
  noData = 'No data available.';


  constructor(
    private translate: TranslateService
  ) {
    this.translate.onLangChange.subscribe((event: LangChangeEvent) => {
      //this.translate.get('Imprint.title').subscribe((translated: string) => {
        //console.log(res);      //=> 'Hello world'
        this.title = this.translate.instant(_('Messages.title'));
        this.typeFilter = this.translate.instant(_('Messages.typeFilter'));
        this.information = this.translate.instant(_('Messages.information'));
        this.success = this.translate.instant(_('Messages.success'));
        this.warning = this.translate.instant(_('Messages.warning'));
        this.error = this.translate.instant(_('Messages.error'));
        this.delete = this.translate.instant(_('Messages.delete'));
        this.deleteSelected = this.translate.instant(_('Messages.deleteSelected'));
        this.deleteAll = this.translate.instant(_('Messages.deleteAll'));
        this.date = this.translate.instant(_('Messages.date'));
        this.type = this.translate.instant(_('Messages.type'));
        this.message = this.translate.instant(_('Messages.message'));
        this.goTo = this.translate.instant(_('Messages.goTo'));
        this.noData = this.translate.instant(_('Messages.noData'));
    });
  }

}

export const BCI_MESSAGES_INTL_PROVIDER = {
  provide: BciMessagesIntl,
  useClass: BciMessagesIntl
};
