import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { FossComponent, FossInfoService } from '../foss-info.service';
import { BreadcrumbsService } from '@bci-web-core/core';

/**
 * @ignore
 */
@Component({
  selector: 'rexroth-foss-info',
  templateUrl: './foss-info.component.html',
  styleUrls: ['./foss-info.component.scss']
})
export class FossInfoComponent implements OnInit {
  components: FossComponent[] = [];
  displayedColumns = ['name', 'version', 'licenses'];

  closeTrigger = () => this.router.navigate(['.'], {relativeTo: this.route});

  constructor(
    private service: FossInfoService,
    private route: ActivatedRoute,
    private breadcrumbsService: BreadcrumbsService,
    private router: Router) { }

  ngOnInit() {
    if (!!this.route.snapshot) {
      this.breadcrumbsService.setTemporaryTitleForPartialRoute(this.route.snapshot, ['foss'], 'Open Source Components');
    }

    this.service.getComponents()
      .subscribe(data => this.components = data);
  }

  getLicenseNames(component: FossComponent): string {
    const names = component.licenses.map<string>(l => l.name).join(', ');
    return !names ? 'UNKNOWN' : names;
  }
}

