import { ChangeDetectorRef, Component, EventEmitter, OnDestroy, OnInit, Output } from '@angular/core';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { BciSharedIntl } from '../shared-intl';

@Component({
  selector: 'bci-filter-picker',
  templateUrl: './filter-picker.component.html',
  styleUrls: [ './filter-picker.component.scss' ]
})
export class FilterPickerComponent implements OnInit, OnDestroy {

  @Output()
  public filter: EventEmitter<string> = new EventEmitter<string>();

  // tslint:disable-next-line:no-output-native
  @Output()
  public close: EventEmitter<string> = new EventEmitter<string>();

  /** Used for component-wide unsubscription on destroy. */
  private ngUnsubscribe = new Subject<any>();

  constructor(public intl: BciSharedIntl, private changeDetector: ChangeDetectorRef) {
  }

  ngOnInit() {
    this.intl.changes
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe(() => this.changeDetector.detectChanges());
  }

  ngOnDestroy(): void {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }

  applyFilters() {
    this.filter.emit();
  }

  closeModal() {
    this.close.emit();
  }

}
