// A single class holing all relevant information for a TASS measurement
// including the raw measurement curve, the TASS parameters used for
// curve analysis, the intermediate (for visualization) and final result
// data.

import { TassResult } from "./tassresult";
import { CurveFile } from "./curvefile";
import { TassParameters } from "./tassparameters";

// The view data result state
export enum TassViewResultType
{
  Error = -1,         // calculated, but has error (so M1/M2,W3/LA not valid)
  NotCalculated = 0,  // not yet calculated
  OkInLimits = 1,     // calculated ok and in limits
  OkNotInLimits = 2,  // calculated ok, but not in limits
}

// more or less the columns to be shown in the UI
export class TassViewDataState
{
  checked: boolean;
  state: string;
  result: TassViewResultType;
  filtered: boolean;  // true, if not visible in list
  constructor() {
    this.checked = false;
    this.state = '';
    this.result = TassViewResultType.NotCalculated;
    this.filtered = false;
  }
}

export class TassViewData
{
  crv: CurveFile;
  par: TassParameters;
  res: TassResult;
  normOnly: boolean = false;
  viewState: TassViewDataState;        // more or less the columns to be shown in the UI

  constructor();
  constructor(curveFile: CurveFile);
  constructor(curveFile: CurveFile, tassParameters: TassParameters);
  constructor(curveFile?: CurveFile, tassParameters?: TassParameters, tassResult?: TassResult) {
    this.crv = !curveFile ? new CurveFile() : curveFile;
    this.par = !tassParameters ? new TassParameters() : tassParameters;
    this.res = !tassResult ? new TassResult() : tassResult;
    this.normOnly = false;
    this.viewState = new TassViewDataState();
  }
}

export enum TassViewEventType
{
  Unknown = 0,
  ItemAdd = 1,
  ItemDel = 2,
  ItemChange = 3,
  ItemAddMultiple = 4,
}

export class TassViewUpdateData
{
  items: TassViewData[];
  event: TassViewEventType;
  changedItem: TassViewData;
  constructor() {
    this.items = [];
    this.event = TassViewEventType.Unknown;
    this.changedItem = null;
  }
}
