import { Component, ElementRef, EventEmitter, Input, OnInit, Output, Renderer2, ViewChild } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';

@Component({
  selector: 'bci-modal-component',
  templateUrl: './modal.component.html',
  styleUrls: ['./modal.component.scss']
})
export class ModalComponent implements OnInit {

  @Input() title = '';
  /** Optional parameter to hide the close icon on the right top corner */
  @Input() closeIcon = true;
  /** Optional parameter to set whether or not to close the modal when clicking on backdrop */
  @Input() closeOnBackdropClick = true;
  /** Optional close handler to intercept close mechanism */
  @Output() closeHandler: EventEmitter<any> = new EventEmitter();

  @ViewChild('buttons', { read: ElementRef }) buttons: ElementRef;

  constructor(public dialogRef: MatDialogRef<ModalComponent>, private renderer: Renderer2) {
    this.dialogRef.disableClose = true;
    this.dialogRef._containerInstance._config.autoFocus = false;
    this.dialogRef._containerInstance._config.disableClose = true;
    this.dialogRef.backdropClick().subscribe(() => {
      if (this.closeOnBackdropClick) {
        this.close();
      }
    });
  }

  ngOnInit() {
    if (!this.buttons || !this.buttons.nativeElement.childNodes[0]) {
      return;
    }

    const buttons = this.buttons.nativeElement.childNodes[0].childNodes;

    this.raiseButton(buttons);

    if (buttons.length === 3) {
      this.renderer.setStyle(buttons[0], 'float', 'left');
    }
  }

  raiseButton(buttons: NodeListOf<HTMLElement>) {
    const raisedButtons = Array.from(buttons).filter(button => button['classList'].contains('mat-raised-button')).length > 0;

    if (buttons.length === 0 || raisedButtons) {
      return;
    }

    const raisedButton = buttons[buttons.length === 3 ? 1 : 0];
    this.renderer.addClass(raisedButton, 'mat-raised-button');
    this.renderer.removeClass(raisedButton, 'mat-button');
    raisedButton.focus();
  }

  close() {
    this.closeHandler.observers.length > 0 ? this.closeHandler.emit() : this.dialogRef.close();
  }

}
