    private baseUrl: string ='/api/'; //'http://localhost:8001/api/'

    private _connect = new BehaviorSubject<Logon>(null);
    readonly connects$ = this._connect.asObservable();
    //private _connectItem: Logon;
    //private connects : Logon[] = [];
    //private lastLogonReq: LogonReq = new LogonReq();

    private lastCurveSeq: number = -1;
    private strCurChn: string = '';
//    private curvePaused: boolean;
