import { Component, OnInit, OnDestroy, AfterViewInit, Input, Output, EventEmitter, ViewChild, ElementRef, OnChanges, SimpleChanges } from "@angular/core";
import { SelectionModel } from "@angular/cdk/collections";
import { Subscription, BehaviorSubject, Observable, fromEvent, forkJoin } from "rxjs";
import { CollectionViewer, DataSource } from "@angular/cdk/collections";
import { TranslateService } from "@ngx-translate/core";
import { NotificationService } from "@bci-web-core/core";

import { CurveFileService } from "../shared/frontend/curvefile-service.service";
import { ParametersService } from "../shared/frontend/parameter.service";
import { TassViewDataService } from "../shared/frontend/tassviewdataservice";
import { BS300IFService } from "../shared/backend/BS300IF-service.service";

import { TassViewData, TassViewUpdateData, TassViewEventType } from "../shared/models/tassviewdata";

import { marker as _ } from "@biesbjerg/ngx-translate-extract-marker";
import { Console } from "console";
import { debounceTime } from "rxjs/operators";
import { CurveFile } from "../shared/models/curvefile";
import { ElectronService } from 'ngx-electron';
//import { EventEmitter } from "stream";

var sortDirection = false

var CurveFiles: TassViewData[] = [];
var filteredVerzeichnis: TassViewData[] = [];

@Component({
    selector: 'bci-kurvenliste',
    templateUrl: 'kurvenliste.component.html',
    styleUrls: ['kurvenliste.component.scss']
})

export class KurvenlisteComponent implements OnInit, AfterViewInit, OnDestroy, OnChanges {

  @Input()
  enableCheckUpdates: boolean = false;

  @Input()
  connected: boolean = false;   // for autoscroll - actually show/hide autoscroll button!

  @Input()
  customExport: boolean = false;

  @Output()
  selChanged: EventEmitter<TassViewData> = new EventEmitter<TassViewData>();

  @Output()
  checkChanged: EventEmitter<TassViewData> = new EventEmitter<TassViewData>();

  @Output()
  filterChanged: EventEmitter<string> = new EventEmitter<string>();

  @Output()
  exportClicked: EventEmitter<number> = new EventEmitter<number>();

  /** Optionally: fix width of chart for complex layouts */
  @Input() maxWidth: number = null;

  selectionChangeSubscription$: Subscription;
  tassViewDataSubscription$: Subscription;
  parameterSubscription$: Subscription;

  selection: SelectionModel<TassViewData>;
  dataSource: PaginatorDataSource<TassViewData>;

  allSelected = false;
  //textFilter: string;
  itemCount = 0;
  selectedItemCount = 0;
  visibleItemCount = 0;
  TassResultText: string = '';
  singleRowSelected = false;
  anyRowSelected = false;
  displayedColumns = ['checkbox','time','name','prg', 'state'];

  private elemDownload: HTMLElement = null;

  constructor(
        private notificationService: NotificationService,
        private curveFileService: CurveFileService,
        private parameterService: ParametersService,
        private translate: TranslateService,
        private BS300IFService: BS300IFService,
        private tassViewDataService: TassViewDataService,
        private electronService: ElectronService
        ){}

  ngOnInit(): void {
        this.selection = new SelectionModel<TassViewData>(false, []);
        this.selectionChangeSubscription$ = this.selection.changed.subscribe(change => {
          console.log('Kurvenliste::OnSelectionChange: ', change)
          // The active selection changed, either the user has clicked something,
          // or the selection was changed externally (e.g. due to autoscroll).
          this.anyRowSelected = change.source.hasValue();
          this.singleRowSelected = change.source.selected.length === 1;
          //this.loadDataSource(0, 10);
          this.updateItemCount();
          // fire an event - get the index first
          this.selChanged.emit(change.source.selected[0]);
        });
        this.tassViewDataSubscription$ = this.tassViewDataService.items$.subscribe((item: TassViewUpdateData) => {
          console.log('Kurvenliste::OnTassViewDataChange: ', item);
          CurveFiles = item.items;
          //filteredVerzeichnis = CurveFiles; //Object.assign([],CurveFiles); Object.assign({},CurveFiles)
          this.updateSearch();    // just in case a filter is active...
          let autoScroll = false;
          if (item.event == TassViewEventType.ItemAdd) {
            // only autoscroll, if the item is not filtered (we get all items here)
            const newItem = CurveFiles[CurveFiles.length - 1];
            autoScroll = !newItem.viewState.filtered;
          }
          this.loadDataSource(false, autoScroll && this.tassViewDataService.autoScroll);
          //console.log(CurveFiles);
        })
    }

  ngOnDestroy() {
    this.tassViewDataSubscription$.unsubscribe();
    this.selectionChangeSubscription$.unsubscribe();
  }

  ngAfterViewInit() {
  }

  openNotification(type: string,text: string): void {
        switch(type){
            case "Error":
                this.notificationService.error(text);
                break;
            case "Warning":
                this.notificationService.warning(text);
                break;
            case "Success":
                this.notificationService.success(text);
                break;
        }
  }

  public setMaxWidth(newMaxWidth): void {
    try {
      //@ts-ignore
      this.densityChart.chart.canvas.style.maxWidth = newMaxWidth + "px";
      //@ts-ignore
      this.seriesChart.chart.canvas.style.maxWidth = newMaxWidth + "px";
    }
    catch {}
  }
  private updateView() {
    if (this.maxWidth != null) {
      // Setup container and canvas sizes before drawing to fix
      // layout issues on complex layouts (workaround for CSS problems)
      this.setMaxWidth(this.maxWidth);
    }
  }
  ngOnChanges(changes: SimpleChanges): void {
    let maxWidth_changed = (changes['maxWidth'] !== undefined) && (changes['maxWidth'].currentValue !== changes['maxWidth'].previousValue);
    if (maxWidth_changed) {
      this.updateView();
    }
  }

  compare(a, b) {
      if(sortDirection){
          b = [a, a = b][0];
      }
      if ( a.name < b.name ){
          return -1;
      }
      if ( a.name > b.name ){
          return 1;
      }
      return 0
    }

    getFileExtension(name: string){
        const fileRegex = /(?:\.([^.]+))?$/;
        return fileRegex.exec(name)[1]
    }

    sort(){
        CurveFiles.sort(this.compare)
        sortDirection = !sortDirection
        this.loadDataSource()
    }
    opencsvFile(){
        //document.querySelector('input').click()
        var element: HTMLElement = document.getElementsByName('csvFile')[0] as HTMLElement
        element.click();
    }

    oncsvFileSelect(input: HTMLInputElement) {
      var promises = [];
      var files: any = Array.from(input.files);
/*
      files.forEach(element => {
        const strExt = this.getFileExtension(element.name).toLowerCase();
        if (strExt == "csv" || strExt == "tra"){
          if (CurveFiles.some(e => e.crv.name === element.name)) {
            this.openNotification("Error", this.translate.instant(_("Notification.Error.Can't_open_two_files_with_the_same_name")) + element.name);
          } else {
            this.curveFileService.readFileAsTextAsync(element).then(txtData => {
              //@ts-ignore
              const crvFile = this.curveFileService.fromCsvText(element.name, txtData);
              var params = this.parameterService.getEditParams()
              this.tassViewDataService.addCurveFile(crvFile, params)
            });
          }
        } else {
          this.openNotification("Error", this.translate.instant(_("Notification.Error.Only_csv_Files_allowed")))
        }
      });
*/
      files.forEach(element => {
        const strExt = this.getFileExtension(element.name).toLowerCase();
        if (strExt == "csv" || strExt == "tra"){
          if (CurveFiles.some(e => e.crv.name === element.name)) {
            this.openNotification("Error", this.translate.instant(_("Notification.Error.Can't_open_two_files_with_the_same_name")) + element.name);
          } else {
            promises.push(this.curveFileService.readFileAsTextAsync2(element));
          }
        }
        else if (strExt == "crv") {
          //promises.push(this.BS300IFService.readFileFromCRV(element.name));
          let promise1 = this.curveFileService.readFileAsBase64Async(element);
          let promise2 = promise1.then(function(result) {
            return this.BS300IFService.readFileFromCRVBuf(element, result.data);
          }.bind(this));
          promises.push(promise2);
        }
        else {
          this.openNotification("Error", this.translate.instant(_("Notification.Error.Only_csv_Files_allowed")))
        }
      });
      // wait for the promises to finish
      var crvFiles: CurveFile[] = [];
      forkJoin(promises).subscribe(o => {
        o.forEach(e => {
          //@ts-ignore
          const strExt = this.getFileExtension(e.file.name).toLowerCase();
          if (strExt == "crv") {
            //@ts-ignore
            const crvFile = this.curveFileService.fromCrvFile(e.file.name, e.data);
            crvFiles.push(crvFile);
          }
          else {
            //@ts-ignore
            const crvFile = this.curveFileService.fromCsvText(e.file.name, e.data);
            crvFiles.push(crvFile);
          }
        });
        // now sort the loaded files before calculation/adding
        crvFiles.sort((a, b) => a.name.localeCompare(b.name));

        // and add to the tassViewDataService
        var params = this.parameterService.getEditParams()
        let newData: TassViewData[] = [];
        crvFiles.forEach(e => {
          let item = new TassViewData(e, params);
          newData.push(item);
        });
        this.tassViewDataService.addAll(newData, true);
        this.loadDataSource();
        this.updateItemCount();
      },
      (error) => {
        this.openNotification("Error", this.translate.instant(_("Notification.Error.InvalidCurveFormat")))
      });
    }

    private updateSearch() {
      if (this.tassViewDataService.PrgFilter == '') return;
      filteredVerzeichnis = CurveFiles.filter(element => {
//        if (this.textFilter == '') return;
//        // element.name.toLowerCase().includes(this.textFilter) ||
//        const visible = (element.crv.program.toString() == this.textFilter);   // full match for PRG only!
//        element.viewState.filtered = !visible;
        return !element.viewState.filtered;
      });
    }

    searchChanged(text: any) {
      //this.textFilter = text.toLowerCase()
      this.tassViewDataService.PrgFilter = text.toLowerCase();
      this.updateSearch();
      this.updateItemCount();
      this.loadDataSource();
      this.filterChanged.emit(this.tassViewDataService.PrgFilter);
    }

    deleteCurveFiles(): void {
      // delete all checked and visible (i.e. not filtered) curves
      this.tassViewDataService.removeCurveFiles((v) => {
        // return true for the curves which shall be deleted
        return v.viewState.checked && (!v.viewState.filtered);  // skip checked
      });
      this.selection.clear();
      this.updateItemCount();
      this.loadDataSource();
    }
    toggleAutoscroll(): void {
      this.tassViewDataService.autoScroll = !this.tassViewDataService.autoScroll;
      //this.BS300IFService.autoCalculate = this.autoScroll;
    }

    private DownloadByHtmlTag(fileName: string, fileType: string, text: string) {
        if (!this.elemDownload) {
          this.elemDownload = document.createElement('a');
        }
        const dataUrl = 'data:' + fileType + ';charset=utf-8,' + encodeURIComponent(text);
        this.elemDownload.setAttribute('href', dataUrl);
        this.elemDownload.setAttribute('download', fileName);
        // simulate a mouse click event
        var event = new MouseEvent("click");
        this.elemDownload.dispatchEvent(event);
      }
    exportCurveFiles(): void {
      if (this.customExport) {
        this.exportClicked.emit(this.selectedItemCount);
      }
      else {
        this.userExport();
        //this.selection.clear();
        //this.updateItemCount();
        //this.loadDataSource();
      }
    }
    userExport() {
      if (!this.electronService.isElectronApp) {
        // download all checked curves
        CurveFiles.forEach(element => {
          if (element.viewState.checked) {
            var fname = element.crv.name;
            var ext = fname.slice((fname.lastIndexOf(".") - 1 >>> 0) + 2).toLowerCase();
            if (ext != "csv") fname = fname + ".csv";
            this.DownloadByHtmlTag(fname, 'text/plain', this.curveFileService.toCsv(element.crv));
          }
        });
      }
      else {
        // create a handler function, which is correctly bound to this...
        let writeHandler = (function (err) {
          if (err != null) {
            console.log(err);
            this.openNotification("Error", err);
          }
        }).bind(this);
        let replyhandler = (function(sender, path) {
          if (!path) return;
          var txt = '';
          // NOTE: this works only inside Electron (node.js):
          //       and requires nodeIntegration = true.
          // For in depth info and (correct) usage (with newer Electron versions), see:
          //  https://stackoverflow.com/questions/37994441/how-to-use-fs-module-inside-electron-atom-webpack-application
          const fs = (<any>window).require("fs");
          // download all checked curves
          CurveFiles.forEach(element => {
            if (element.viewState.checked) {
              var fname = element.crv.name;
              var ext = fname.slice((fname.lastIndexOf(".") - 1 >>> 0) + 2).toLowerCase();
              if (ext != "csv") fname = fname + ".csv";
              let txt = this.curveFileService.toCsv(element.crv);
              let fullpath = path + '\\' + fname;
              console.log("writing: " + fullpath);
              fs.writeFile(fullpath, txt, writeHandler);
            }
          });
          this.electronService.shell.beep();
        }).bind(this);
        this.electronService.ipcRenderer.once("save-folder-reply", replyhandler);
        this.electronService.ipcRenderer.send("save-folder", null);
      }
    }

    ToggleAllCheck() {
      // NOTE: This respects the filter settings, i.e. only checks the
      //       currently visible items, not neccessarily all...
      const newState = !this.allSelected;
        CurveFiles.forEach(element => {
          if (!element.viewState?.filtered) {
            this.tassViewDataService.setCheck(element, newState);
          }
        });
        this.updateItemCount();
        this.checkChanged.emit(null);
    }
    ToggleCheck(tassViewData: TassViewData) {
      const newState = !tassViewData.viewState.checked;
      // now also update the model, so dependent components get notified
      this.tassViewDataService.setCheck(tassViewData, newState);
      this.updateItemCount();
      this.checkChanged.emit(tassViewData);
//      this.loadDataSource();
    }

    private updateItemCount() {
      if (this.tassViewDataService.PrgFilter?.length > 0){
          this.itemCount = filteredVerzeichnis.length;
        } else {
          this.itemCount = CurveFiles.length;
        }
        //this.selectedItemCount = Object.values(CurveFiles).filter(item => item.viewState.checked === true).length;
        this.selectedItemCount = CurveFiles.filter(item =>
          (item.viewState.checked === true && !item.viewState.filtered)
        ).length;
        if (this.selectedItemCount == this.itemCount && this.selectedItemCount > 0){
          this.allSelected = true;
        } else {
          this.allSelected = false;
        }
        // get the number of visible items
        this.visibleItemCount = CurveFiles.filter(item =>
          !item.viewState.filtered
        ).length;
    }

    listSelectionChanged(row) {
        if (row == null) {
          // Edit params changed - so only refresh, do not change the actual selection
        }
        else {
          // change the selection to the given row, update the selection model
          if (this.selection) {
            this.selection.select(row);   //selection.toggle(row);
            this.updateItemCount();
          }
        }
      }

    private loadDataSource(showNothing: boolean = false, autoScroll: boolean = false) {
      if (showNothing) {
        this.dataSource = new PaginatorDataSource<TassViewData>();
        return;
      }
      if (this.tassViewDataService.PrgFilter?.length > 0){
        this.dataSource = new PaginatorDataSource<TassViewData>(filteredVerzeichnis);
        if (autoScroll && this.selection && filteredVerzeichnis.length > 0) {
          this.selection.select(filteredVerzeichnis[filteredVerzeichnis.length-1]);
        }
      } else {
        this.dataSource = new PaginatorDataSource<TassViewData>(CurveFiles);
        if (autoScroll && this.selection && CurveFiles.length > 0) {
          this.selection.select(CurveFiles[CurveFiles.length-1]);
        }
      }
    }
}

//-----------------------------------------------------------------------------------------------

export class PaginatorDataSource<T> implements DataSource<T> {

    _allData: T[] = [];
//    _paginated: T[] = [];
//    pageData: {
//        pageSize: number;
//        pageIndex: number;
//    } = {
//        pageSize: 100,
//        pageIndex: 0
//    };
    private readonly _renderData = new BehaviorSubject<T[]>([]);

    constructor(data?: T[]) {
        this.data = data || [];
    }

//    get pageSize(): number {
//        return this.pageData.pageSize;
//    }
//    set pageSize(pageSize: number) {
//        this.pageData.pageSize = pageSize;
//        this._filter();
//    }

//    get pageIndex(): number {
//        return this.pageData.pageIndex;
//    }
//    set pageIndex(index: number) {
//        this.pageData.pageIndex = index;
//        this._filter();
//    }

//get paginatedData(): T[] {
//  return [ ...this._paginated ];
//}

    get numberOfItems(): number {
        return this._allData.length;
    }

    get data(): T[] {
        return [ ...this._allData ];
    }

    set data(data: T[]) {
        this._allData = data;
        this._filter();
    }

    _filter() {
        //const startFromPage = this.pageData.pageIndex === 0 ? 0 : this.pageData.pageSize * this.pageData.pageIndex;
        //const pageDataSize = startFromPage + this.pageData.pageSize;
        //this._paginated = this._allData.slice(startFromPage, pageDataSize);
        //this._renderData.next(this._paginated);
        this._renderData.next(this._allData);
    }

    connect(collectionViewer: CollectionViewer): Observable<T[] | ReadonlyArray<T>> {
      return this._renderData;
    }
    disconnect(collectionViewer: CollectionViewer): void {
    }
}
