///! @brief Class to keep all results (curve + calculation) for the current "project"
///! Implements an observable to update the GUI for the "current" curve and parameter
///! set displayed in the GUI, also a "wrapper" for the data dhown in the list.
@Injectable({ providedIn: 'root' })
export class TassResultService {

    private _result = new BehaviorSubject<TassResultData>(new TassResultData());
    readonly results$ = this._result.asObservable()

    //private results: TassResult[] = []

    constructor(
      private translate: TranslateService
    ) {}

    publishNewResult(item: TassResult, crv: CurveFile) {
      this.create(item, crv);
    }

    private create(item: TassResult, crv: CurveFile) {
        const data: TassResultData = {
          tassResult: Object.assign(new TassResult(), item),
          curve: crv  // keep this as a reference!
        }
        //this.results.push(item)
        //const oCopy = Object.assign(new TassResult(), item);
        //this._result.next(oCopy);
        console.log('TassResultService:publish()');
        this._result.next(data);
    }

    //getAllResults() {
    //    return this.results
    //}
    getCurrentResult(): TassResultData {
      // the BehvioralSubject allows accessing the "current" value directly:
      return this._result.getValue();
    }

    public getLocalizedErrStr(errCode: number): string {
      // get a unique string to use as translation key for ngx-translate
      var strKey = TassErrorStrMap.get(errCode);
      if (strKey == null) {
        strKey = _("TassErrCode.UNKNOWN");
      }
      const strLocal = this.translate.instant(strKey, {code: errCode});
      return strLocal;
    }
}
