import { Component, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { AlertService, AlertType, PrimaryButton } from '@bci-web-core/core';
import { Observable } from 'rxjs';

import { ParametersService } from '../../shared/frontend/parameter.service';

import { TassParameters } from '../../shared/models/tassparameters';

@Component({
  selector: 'bci-modalnew',
  templateUrl: './modalnew.component.html',
  styleUrls: ['./modalnew.component.scss']
})
export class ModalNewComponent {

    params$: Observable<TassParameters>;

    M1_cali: number;
    M2_cali: number;
    W3_cali: number;
    SN: string;
    LocalParams: TassParameters = new TassParameters();

    constructor(
        private dialogRef: MatDialogRef<ModalNewComponent>,
        private alertService: AlertService,
        private parameterService: ParametersService,
        @Inject(MAT_DIALOG_DATA) public data: any
    ) {
      // Initialize default local params
      this.LocalParams.sB_RNG = 70.0;
      this.LocalParams.sB_T   = 0.5;
      this.LocalParams.ms     = 0.0;
      this.LocalParams.me     = 0.0;
      this.LocalParams.t1     = 0.0;
      this.LocalParams.aL_Min = 3;
      this.LocalParams.aR_Min = 3;
      this.LocalParams.aM_MinR      = 0.95;
      this.LocalParams.aL_AR_MinR   = 0.95;
      this.LocalParams.sha1         = null;
      this.LocalParams.sn           = null;
      this.LocalParams.valid        = false;
      this.LocalParams.filename     = null;
    }

    save(): void {
        this.LocalParams.m1_L = this.M1_cali - (this.M1_cali / 10);
        this.LocalParams.m1_H = (this.M1_cali / 10) + this.M1_cali;
        this.LocalParams.m1_cali = this.M1_cali;
        this.LocalParams.m2_L = this.M2_cali - (this.M2_cali / 10);
        this.LocalParams.m2_H = (this.M2_cali / 10) + this.M2_cali;
        this.LocalParams.m2_cali = this.M2_cali;
        this.LocalParams.w3_L = this.W3_cali - (this.W3_cali / 10);
        this.LocalParams.w3_H = (this.W3_cali / 10) + this.W3_cali;
        this.LocalParams.w3_cali = this.W3_cali;
        this.LocalParams.lA_T = this.M2_cali * 0.05;  // 5% of M2_Cali
        this.LocalParams.ms = this.M1_cali * 0.5;  // 50% of M1_Cali
        this.LocalParams.sn = this.SN;
        this.parameterService.publish(this.LocalParams);
        this.dialogRef.close();
    }


    close(): void {
        console.log('Closed by backdrop or icon');
        this.dialogRef.close();
    }

    delete(): void {
        const ref = this.alertService.openAlertBox({
            title: 'Delete Item?',
            description: 'This action will delete this item.',
            type: AlertType.Warning,
            leftButtonTitle: 'Delete',
            rightButtonTitle: 'Cancel',
            primaryButton: PrimaryButton.RightButton
    });

    ref.afterClosed().subscribe(res => {
        if (res === 'left') {
        console.log('Delete Item');
        this.dialogRef.close();
        }
    });
    }

    cancel(): void {
/*
    const ref = this.alertService.openAlertBox({
        title: 'Really abort?',
        description: 'Discarding the draft will delete any changes on this item.',
        type: AlertType.Warning,
        leftButtonTitle: 'Discard',
        rightButtonTitle: 'Cancel',
        primaryButton: PrimaryButton.RightButton
    });

    ref.afterClosed().subscribe(res => {
        if (res === 'left') {
        this.dialogRef.close();
        }
    });
*/
        this.dialogRef.close()
    }
}
