import { Component, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { PrimaryButton } from '@bci-web-core/core';
import { Observable } from 'rxjs';

@Component({
  selector: 'bci-modallimits',
  templateUrl: './modallimits.component.html',
  styleUrls: ['./modallimits.component.scss']
})
export class ModalLimitsComponent {

    PercentValue: number;

    constructor(
        private dialogRef: MatDialogRef<ModalLimitsComponent>,
        @Inject(MAT_DIALOG_DATA) public data: any
    ) {
      // Initialize default local params
      this.PercentValue = 5;
    }

    save(): void {
        this.dialogRef.close(this.PercentValue);
    }

    cancel(): void {
      this.dialogRef.close(null);
    }

    close(): void {
      this.dialogRef.close(null);
    }
}
