  public setMaxWidth(newMaxWidth): void {
    try {
      //@ts-ignore
      this.densityChart.chart.canvas.style.maxWidth = newMaxWidth + "px";
      //@ts-ignore
      this.seriesChart.chart.canvas.style.maxWidth = newMaxWidth + "px";
    }
    catch {}
}

  private updateView() {
    if (this.maxWidth != null) {
      // Setup container and canvas sizes before drawing to fix
      // layout issues on complex layouts (workaround for CSS problems)
      this.setMaxWidth(this.maxWidth);
    }
    this.dcChart_draw();
    this.scChart_draw();
  }

  private getPointRadius(ctx) {
    const index = ctx.dataIndex;
    //const value = ctx.dataset.data[index];
    if (this.selectIdx == index) {
      return 6;
    }
    return 4;
  }

