  dcChart_clear()
  {
    this.dcData = {
      labels: [],
      datasets: [
        {  // series 2 - Mittelwert und Sollwert
          data: [],
          label: '',
          pointRadius: 4,   // feste Gre
          showLine: true,
          borderColor: 'black',
          borderWidth: 1,
          pointBorderColor: ['black'],       // array of colors
          pointBackgroundColor: [BOSCH_COLORS.BoschSignalYellow,BOSCH_COLORS.BoschLightBlue], // , 'yellow', 'blue'
          pointHoverRadius: 5,
          fill: false,
          tension: 0,
          datalabels: {
            display: false,
          }
        },
        {  // series 1
          data: [],
          label: '',
          fill: false,
          showLine: true,
          borderWidth: 1,
          borderColor: 'black', // BOSCH_COLORS.BoschLightBlue,
          pointRadius: 4,
          pointBorderColor: ['darkgray'],       // array of colors
          pointBackgroundColor: ['rgba(128,128,128,128)'], // , 'yellow', 'blue'
          pointHoverRadius: 5,
          tension: 0,
          datalabels: {
            display: false,
          },
        }
      ]
    };
    // setup the callbacks to have the correct context...
    let pr = (function(ctx) {
      return this.getPointRadius(ctx);
    }).bind(this);
    this.dcData.datasets[1].pointRadius = pr;   // assign the bound function!
  }
  dcChart_initAnnotations() {
    this.dcOptions.plugins.annotation = {
      annotations: [],
    };
    return this.dcOptions.plugins.annotation.annotations;
  }

  dcChart_draw()
  {
    if (this.caption != 'W3' || this.data.y2 == null || this.data.y2.data == null) {
    }
    // the W3 chart has an additional y2-axis to show T1 values
    this.dcChart_clear();
    var optAnnotations = this.dcChart_initAnnotations();

    // generate the chart data series
    const y1 = this.data.y;
    const y2 = this.data.y2;
    var cd = this.dcData;
    cd.datasets[0].data.push({ x: y1.cali, y: 0 });
    cd.datasets[0].data.push({ x: y1.avg,  y: 0});
    this.data.y.data.forEach((val, idx) => {
      cd.datasets[1].data.push({ x: val.y, y: 0 });
    });
  }

