  scChart_clear(hasSecondAxis: Boolean)
  {
    this.scData = {
      labels: [],
      datasets: [
        {  // series 1 - Hauptachse Y
          data: [],
          label: '',
          pointRadius: 4,
          showLine: true,
          borderColor: 'black',
          borderWidth: 1,
          pointBorderColor: [],       // array of colors
          pointBackgroundColor: [],
          pointHoverRadius: 5,
          fill: false,
          tension: 0,
          datalabels: {
            display: false,
          }
        },
      ]
    }
    if (this.caption == 'W3') {
      this.scOptions.scales.y.title.text = this.caption + ' []';
    } else {
      this.scOptions.scales.y.title.text = this.caption + ' [Nm]';
    }
    // setup the callbacks to have the correct context...
    let pr = (function(ctx) {
      return this.getPointRadius(ctx);
    }).bind(this);
    this.scData.datasets[0].pointRadius = pr;   // assign the bound function!

    if (hasSecondAxis) {
      this.scData.datasets.push(
        {  // series 2 - Y2-Achse (nut fr T1)
          yAxisID: 'y2',
          data: [],
          label: '',
          pointRadius: 4,
          showLine: false,
          borderColor: 'black',
          borderWidth: 1,
          pointBorderColor: BOSCH_COLORS.BoschDarkGray,       // array of colors
          pointBackgroundColor: BOSCH_COLORS.BoschLightGray,
          pointHoverRadius: 5,
          fill: false,
          tension: 0,
          datalabels: {
            display: false,
          }
        }
      );
      this.scData.datasets[1].pointRadius = pr;   // assign the bound function!
    } else {
      this.scOptions.scales.y2.display = false;
    }
  }

  scChart_initAnnotations() {
    this.scOptions.plugins.annotation = {
      annotations: [],
    };
    return this.scOptions.plugins.annotation.annotations;
  }

  scChart_draw()
  {
    const hasT1 = (this.caption == 'W3');
    this.scChart_clear(hasT1);
    var optAnnotations = this.scChart_initAnnotations();

    // add annotations (min/max/avg/calib)
    const y = this.data.y;
    if (y.min > 0) {
      this.addAnno_H(optAnnotations, y.min, 'red', [5, 5]);
    }
    if (y.max > 0) {
      this.addAnno_H(optAnnotations, y.max, 'red', [5, 5]);
    }
    if (y.cali > 0) {
      this.addAnno_H(optAnnotations, y.cali, BOSCH_COLORS.BoschSignalYellow);
    }
    if (y.avg > 0) {
      this.addAnno_H(optAnnotations, y.avg, BOSCH_COLORS.BoschLightBlue, [15, 3, 3, 3]);
    }

    // generate the chart data series
    const y1 = this.data.y;
    var cd = this.scData;
    this.data.y.data.forEach((val, idx) => {
      cd.datasets[0].data.push({ x: idx, y: val.y });
      var color = 'yellow';
      const isLo = (val.y < y1.min);
      const isHi = (val.y > y1.max);
      if (isLo || isHi) {
        color = BOSCH_COLORS.BoschRed;
      } else {
        color = BOSCH_COLORS.BoschLightGrayB50;
      }
      // @ts-ignore
      cd.datasets[0].pointBackgroundColor.push(color);
    });
    cd.datasets[0].pointBorderColor = BOSCH_COLORS.BoschDarkGray; //cd.datasets[0].pointBackgroundColor;

    if (hasT1) {
      const y2 = this.data.y2;
      this.data.y2.data.forEach((val, idx) => {
        cd.datasets[1].data.push({ x: idx, y: val.y });
      });
    }

  }

  // ------------------- end charts ------------------------------------------------------

  public zoomReset(): void {
    this.densityChart?.chart?.resetZoom();
    this.seriesChart?.chart?.resetZoom();
//    // test:
//    this.densityChart?.chart?.resize();
//    this.seriesChart?.chart?.resize();
  }
  private formatValue(value: number, digits: number = 0): string {
    if (value == null) return '';
    if (digits == 0) return (+value).toFixed(0);
    var s = (+value).toPrecision(digits);
    if (s.indexOf('e') >= 0) {
        // this can only happen if the number is too large, so only show the int part
        s = (+value).toFixed(0);
    }
    return s;
}

  public fmtVal(v: number): string {
    if (this.caption == 'LA') {
        return this.formatValue(v, 3);
    }
    return this.formatValue(v, 3);

  }

