import { CdkDragDrop, CdkDragEnd, CdkDragStart } from '@angular/cdk/drag-drop';
import { Component, EventEmitter, Input, OnDestroy, OnInit, Output, TemplateRef, ViewChild } from '@angular/core';
import { MatSelectionList } from '@angular/material/list';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { BciPicklistIntl } from '../picklist-intl';

import { DragInstance, DragState, PicklistItem } from '../picklist.model';
import { PicklistService } from '../picklist.service';
import { BciPicklistResponsibility } from '../picklist.types';

/** A list of items that can be selected and dragged/dropped. */
@Component({
  selector: 'bci-drag-drop-list',
  templateUrl: './drag-drop-list.component.html',
  styleUrls: ['./drag-drop-list.component.scss']
})
export class DragDropListComponent implements OnInit, OnDestroy {
  /** Items received from the {@link PicklistService}, depending on the list's responsibility. */
  items: PicklistItem[] = [];

  /** Whether the items list acts as source or target. */
  @Input() responsibility: BciPicklistResponsibility;

  /** Optional item template. If undefined the items are displayed using their string representation. */
  @Input() itemTemplate: TemplateRef<any>;

  /** Whether items can be manually repositioned via drag and drop. */
  @Input() manualSorting = false;

  /** Placeholder that is shown the list is empty. */
  @Input() dropOverlayPlaceholder: string;

  /** Whether drop on self must be disabled. */
  @Input() dropSelfDisabled = false;

  @Input() dragInstance: DragInstance;

  @Output() dragInstanceChange = new EventEmitter<DragInstance>();

  private ngUnsubscribe = new Subject<any>();

  private readonly numPreviewItems = 4;

  @ViewChild(MatSelectionList) private selectionList: MatSelectionList;

  constructor(public picklistService: PicklistService, public picklistIntl: BciPicklistIntl) {
  }

  /** Whether the drop area must be highlighted. */
  get highlightDropArea(): boolean {
    return this.responsibility === 'target' && this.dragInstance.source === 'source' && this.dragInstance.state === DragState.Dragging
      || this.responsibility === 'target' && this.dragInstance.source === 'target' && this.dragInstance.state === DragState.Dragging && this.manualSorting && !this.dropSelfDisabled
      || this.responsibility === 'source' && this.dragInstance.source === 'target' && this.dragInstance.state === DragState.Dragging;
  }

  get isSourceList(): boolean {
    return this.responsibility === 'source';
  }

  /**
   * Items of the current {@link DragDropListComponent#dragInstance drag state's} if available.
   */
  selectedPreviewItems(): PicklistItem[] {
    return this.dragInstance.draggedItems.filter((item, index) => index < this.numPreviewItems);
  }

  remainingPreviewItems(): number {
    return this.dragInstance.draggedItems.length - this.numPreviewItems;
  }

  ngOnInit(): void {
    switch (this.responsibility) {
      case 'source': {
        this.picklistService.processedSourceItems
          .pipe(takeUntil(this.ngUnsubscribe))
          .subscribe(list => {
            if (this.selectionList && this.selectionList.selectedOptions) {
              // since items list will reset, selected items should also reset
              this.selectionList.selectedOptions.clear();
            }
            this.items.length = 0;
            this.items.push(...list);
          });
        break;
      }
      case 'target': {
        this.picklistService.processedTargetItems
          .pipe(takeUntil(this.ngUnsubscribe))
          .subscribe(list => {
            if (this.selectionList && this.selectionList.selectedOptions) {
              // since items list will reset, selected items should also reset
              this.selectionList.selectedOptions.clear();
            }
            this.items.length = 0;
            this.items.push(...list);
          });
        break;
      }
    }
  }

  ngOnDestroy(): void {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }

  /**
   * Returns a context depending on the type of the value.
   * @param value Item context
   */
  getItemContext(value: any): any {
    return typeof value === 'object' ? value : { value: value };
  }

  /**
   * Called when the item selection changes.
   * @param selected
   * @param item Selected item.
   */
  onSelectionChange(selected: boolean, item: PicklistItem): void {
    item.selected = selected;
    switch (this.responsibility) {
      case 'source': {
        this.picklistService.updateSourceSelection();
        break;
      }
      case 'target': {
        this.picklistService.updateTargetSelection();
        break;
      }
    }
  }

  /**
   * Called when an item has been double clicked.
   * @param item Selected item that has been double clicked.
   */
  onDoubleClick(item: PicklistItem): void {
    switch (this.responsibility) {
      case 'source': {
        this.picklistService.addItems(item);
        break;
      }
      case 'target': {
        this.picklistService.removeItems(item);
        break;
      }
    }
  }

  onDrop(event: CdkDragDrop<PicklistItem[]>): void {
    const selectedItems = event.previousContainer.data.filter(elem => elem.selected);
    if (selectedItems.length === 1) {
      selectedItems[0].selected = false;
    }

    // NOTE: Index is needed due to the difference between item array and list with placeholder.
    const index = event.previousIndex < event.currentIndex ? event.currentIndex + 1 : event.currentIndex;
    if (this.responsibility === 'target' && event.previousContainer === event.container && this.manualSorting && !this.dropSelfDisabled) {
      // Reposition on target
      this.picklistService.repositionTargetItems(index, ...selectedItems);
    } else if (this.responsibility === 'target' && event.previousContainer !== event.container) {
      // Drag to target
      this.picklistService.insertItems(event.currentIndex, ...selectedItems);
    } else if (this.responsibility === 'source' && event.previousContainer !== event.container) {
      // Drag to source
      this.picklistService.removeItems(...selectedItems);
    }
  }

  onDragStarted(event: CdkDragStart): void {
    event.source.data.selected = true;
    event.source.dropContainer.data.forEach( item => {
      item.dragging = item.selected;
    });
    this.dragInstance = {
      source: this.responsibility,
      state: DragState.Dragging,
      draggedItems: event.source.dropContainer.data.filter( item => item.dragging === true)
    };
    this.dragInstanceChange.emit(this.dragInstance);
  }

  onDragEnded(event: CdkDragEnd): void {
    event.source.dropContainer.data.forEach(item => {
      item.dragging = false;
    });
    this.dragInstance = {
      source: 'none',
      state: DragState.Idle,
      draggedItems: []
    };
    this.dragInstanceChange.emit(this.dragInstance);
  }
}
