import { A11yModule } from '@angular/cdk/a11y';
/* CDK imports */
import { CommonModule } from '@angular/common';
import { HttpClientModule, HttpClient } from '@angular/common/http';
import { CUSTOM_ELEMENTS_SCHEMA, NgModule } from '@angular/core';
import { ReactiveFormsModule } from '@angular/forms';
/* Material imports*/
import { MatTableModule } from '@angular/material/table';
import { MatBadgeModule } from '@angular/material/badge';
import { MatTooltipModule } from '@angular/material/tooltip';
import { BciChartsModule } from '@bci-web-core/charts';
import { MatCheckboxModule } from '@angular/material/checkbox';
import { MatNativeDateModule } from '@angular/material/core';
import { MatFormFieldModule } from '@angular/material/form-field';
import { MatProgressBarModule } from '@angular/material/progress-bar';
import { MatRadioModule } from '@angular/material/radio';
import { MatIconModule } from '@angular/material/icon';
import { MatInputModule } from '@angular/material/input';
import { MatListModule } from '@angular/material/list';
import { MatSelectModule } from '@angular/material/select';
import { MatSlideToggleModule } from '@angular/material/slide-toggle';
import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { BciComponentModule, BciCoreModule, BciLayoutModule, BciSharedModule, CloseDetailsGuard } from '@bci-web-core/core';
import { environment } from '../environments/environment';
import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { MatSliderModule } from '@angular/material/slider';
import { FormsModule } from '@angular/forms';
import { MatTabsModule } from '@angular/material/tabs'
import { FossInfoModule } from './foss-info/foss-info.module';

import { InbetriebnahmeComponent } from './inbetriebnahme/inbetriebnahme.component';
import { AnalyseComponent } from './analyse/analyse.component';
import { KalibrierungComponent } from './kalibrierung/kalibrierung.component';
import { EinstellungenComponent } from './einstellungen/einstellungen.component';
import { KurvenlisteComponent } from './kurvenliste/kurvenliste.component';

import { ModalConnectComponent } from './inbetriebnahme/modalconnect/modalconnect.component';
import { ModalNewComponent } from './inbetriebnahme/modalnew/modalnew.component';
import { ModalLimitsComponent } from './inbetriebnahme/modallimits/modallimits.component';

import { TassService } from './shared/backend/TASS-service.service';
import { CurveFileService } from './shared/frontend/curvefile-service.service';
import { BS300IFService } from './shared/backend/BS300IF-service.service';
import { TassViewDataService } from './shared/frontend/tassviewdataservice';

import { SignificantNumberPipe } from './shared/pipes/significant-number.pipe';

import { NgChartsModule } from 'ng2-charts';
import { TassScatterChartComponent } from './tass-scatter-chart/tass-scatter-chart.component';

import { TranslateModule, TranslateLoader} from '@ngx-translate/core';
import { TranslateHttpLoader} from '@ngx-translate/http-loader';
import { NgxElectronModule } from 'ngx-electron';
import { AnaylseChartComponent } from './analyse/anaylse-chart/anaylse-chart.component';
import { ModalExportComponent } from './analyse/modalexport/modalexport.component';

export function createTranslateLoader(http: HttpClient) {
    return new TranslateHttpLoader(http, './assets/i18n/', '.json');
}

@NgModule({
  declarations: [
    InbetriebnahmeComponent,
    AnalyseComponent,
    KalibrierungComponent,
    EinstellungenComponent,
    TassScatterChartComponent,
    KurvenlisteComponent,
    ModalConnectComponent,
    ModalNewComponent,
    ModalLimitsComponent,
    AppComponent,
    AnaylseChartComponent,
    ModalExportComponent,
    SignificantNumberPipe
  ],
  imports: [
    FormsModule,
    AppRoutingModule,
    BrowserModule,
    CommonModule,
    HttpClientModule,
    BrowserAnimationsModule,
    BciCoreModule.forRoot({
      prod_environment: environment.production,
      core_config_url: '/assets/config/config.json',
    }),
    BciComponentModule,
    BciChartsModule,
    BciSharedModule,
    BciLayoutModule,
    ReactiveFormsModule,
    MatNativeDateModule,
    MatTooltipModule,
    MatBadgeModule,
    MatTabsModule,
    MatCheckboxModule,
    MatSelectModule,
    MatListModule,
    A11yModule,
    MatTableModule,
    MatIconModule,
    MatInputModule,
    MatFormFieldModule,
    MatSliderModule,
    ReactiveFormsModule,
    MatRadioModule,
    MatSelectModule,
    MatProgressBarModule,
    MatSlideToggleModule,
    FossInfoModule,
    NgChartsModule,
    NgxElectronModule,
    TranslateModule.forRoot({
        loader: {
            provide: TranslateLoader,
            useFactory: (createTranslateLoader),
            deps: [HttpClient]
        }
        , defaultLanguage: 'en'
    }),
  ],
  providers: [
      CloseDetailsGuard,
      TassService,
      CurveFileService,
      BS300IFService,
      TassViewDataService
    ],
  bootstrap: [AppComponent],
  schemas: [CUSTOM_ELEMENTS_SCHEMA],
})
export class AppModule {}
