import { Directive, ElementRef, Input, isDevMode, OnChanges, SimpleChanges } from '@angular/core';

@Directive({
  selector: '[bciFullscreen]'
})
export class FullscreenDirective implements OnChanges {

  @Input() bciFullscreen: boolean;

  constructor(private element: ElementRef) { }

  ngOnChanges(changes: SimpleChanges): void {

    if (!document.fullscreenEnabled &&
      !document['mozFullScreenEnabled'] &&
      !document.documentElement['webkitRequestFullScreen']) {
      if (isDevMode()) {
        console.log('Fullscreen not enabled on your Browser');
      }
      return;
    }

    if (this.bciFullscreen) {
      return this.activateFullscreen();
    }

    if (!!changes.bciFullscreen.previousValue && !changes.bciFullscreen.currentValue) {
      this.deactivateFullscreen();
    }
  }

  activateFullscreen(): void {
    this.execute(this.element.nativeElement, [
      'requestFullscreen',
      'webkitRequestFullScreen',
      'mozRequestFullScreen',
      'msRequestFullscreen'
    ]);
  }

  deactivateFullscreen() {
    try {
      this.execute(document, [
        'exitFullscreen',
        'mozCancelFullScreen',
        'webkitExitFullscreen',
        'msExitFullscreen'
      ]);
    } catch (e) { }
  }

  private execute(element: Document | ElementRef, methods: Array<string>) {
    const call = methods.filter(method => !!element[method])[0];
    if (call) {
      return element[call]();
    }
    if (isDevMode()) {
      console.log('Fullscreen not enabled on your Browser');
    }
  }
}
