import { Component, OnInit, Host } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Observable } from 'rxjs';
import { DetailsComponent, MasterDetailViewComponent } from '@bci-web-core/core';
import { FossComponent, License, FossInfoService } from '../foss-info.service';

/**
 * @ignore
 */
@Component({
  selector: 'rexroth-foss-info-details',
  templateUrl: './foss-info-details.component.html',
  styleUrls: ['./foss-info-details.component.scss']
})
export class FossInfoDetailsComponent implements OnInit, DetailsComponent {
  component: FossComponent;

  constructor(
    private masterDetailView: MasterDetailViewComponent,
    private service: FossInfoService,
    private route: ActivatedRoute
  ) { }

  ngOnInit() {
    this.route.paramMap.subscribe(params => this.getComponent(params.get('id')));
    this.masterDetailView.openDetails();
  }

  private getComponent(id: string) {
    if (!!this.service.components) {
      this.component = this.service.components[id];
      return;
    }
    this.service.getComponents()
      .subscribe(data => this.component = data[id]);
  }

  htmlToPlainText(html: string): string {
    const element = document.createElement('div');
    element.innerHTML = html;
    return element.innerText;
  }

  canCloseDetails(): boolean | Observable<boolean> {
    return true;
  }

  closeDetails(): boolean | Observable<boolean> {
    return this.masterDetailView.closeDetails();
  }

  getSpdxLink(license: License): string {
    return `https://spdx.org/licenses/${license.spdx}.html`;
  }
}
